import get from 'lodash/get'

export function parseSlot (slot) {
  if (!slot) {
    return null
  }

  return {
    ...slot.storage_inventory,
    quantity: get(slot, 'storage_inventory.sku_batch.quantity', 0),
    sku_id: get(slot, 'storage_inventory.sku_batch.sku_id'),
    ordinal: slot.ordinal,
    displayOrdinal: slot.display_ordinal,
    reservations: get(slot, 'reservations', []),
    reservationCount: get(slot, 'storage_inventory.reservations', [])
      .map(x => x.quantity)
      .reduce((acc, cur) => acc + cur, 0),
  }
}

export function parseTotes (tote) {
  const newTote = {
    tote_id: tote.tote_id,
    tote_orientation: tote.tote_orientation,
    tote_partitioning: get(tote, 'tote_type.tote_partitioning'),
    tote_height: get(tote, 'tote_type.tote_height'),
    tote_status: tote.tote_status,
    slots: tote.slots.map(parseSlot),
  }
  return newTote
}

function parseInductSlot (slot) {
  return {
    ordinal: parseInt(slot.ordinal),
    quantity: parseInt(slot.quantity),
    sku_id: slot.sku_id,
    supply_item_id: slot.supply_item_id
  }
}

export function parseInductTote ({ leftSlot, centerSlot, rightSlot, tote_id }) {
  const slots = []

  if (!leftSlot.disabled) {
    slots.push(parseInductSlot(leftSlot))
  }

  if (!centerSlot.disabled) {
    slots.push(parseInductSlot(centerSlot))
  }

  if (!rightSlot.disabled) {
    slots.push(parseInductSlot(rightSlot))
  }

  return {
    slots,
    tote_id,
  }
}
