import React from 'react'
import { useTranslation } from 'react-i18next'

import Button from 'components/Button'
import Checkbox from 'components/Checkbox'
import { Table, Head, Row, Cell } from 'components/Table'
import OrderInfoCell from './OrderInfoCell'
import { arraysEqual } from '../../../helpers'

const OrdersList = props => {
  const { orders, onItemSelect, selectedItems, onItemSelectAll, onItemDelete, onItemResend } = props
  const { t } = useTranslation()

  const allChecked =
    arraysEqual(
      orders.map(x => x.order_id),
      selectedItems,
    ) && selectedItems.length > 0

  const orderHeaders = [
    { id: 'select_all', label: <Checkbox onChange={onItemSelectAll} checked={allChecked} /> },
    { id: 'order_id', label: 'Order ID' },
    { id: 'order_type', label: 'Type' },
    { id: 'order_status', label: 'Order State' },
    { id: 'order_info', label: 'Order Information' },
    { id: 'delivery_tote', label: 'Delivery Tote' },
    { id: 'created_at', label: 'Created At' },
    { id: 'delivered_at', label: 'Delivered At' },
    { id: 'collected_at', label: 'Collected At' },
  ]

  const onSelect = e => {
    const { checked, name } = e.target
    onItemSelect(name, checked)
  }

  return (
    <Table className='rcs-orders__table' head={<Head headers={orderHeaders} />}>
      {orders.map(order => (
        <Row key={order.order_id}>
          <Cell>
            <Checkbox
              checked={selectedItems.indexOf(order.order_id) > -1}
              onChange={onSelect}
              name={order.order_id}
            />
          </Cell>
          <Cell>{order.order_id}</Cell>
          <Cell>{order.order_status}</Cell>
          <OrderInfoCell data={order.order_lines} />
          <Cell>{order.order_type}</Cell>
          <Cell>{order.delivery_tote}</Cell>
          <Cell>{order.created_at}</Cell>
          <Cell>{order.delivered_at}</Cell>
          <Cell>{order.collected_at}</Cell>
          <Cell className='rcs-orders__table-actions-cell'>
            <Button onClick={() => onItemDelete([order.order_id])} btnType='table'>
              {t('Delete')}
            </Button>
            <Button onClick={() => onItemResend([order.order_id])} btnType='table'>
              {t('Resend')}
            </Button>
          </Cell>
        </Row>
      ))}
    </Table>
  )
}

export default OrdersList
