import React from 'react'
import {compose} from 'recompose'
import {withTranslation} from 'react-i18next'
import {withRouter} from 'react-router'
import {withApi, withConfirmation, withSupplyInfo} from 'providers'
import {parseError} from 'helpers'
import SlotResupply from './ToteResupply/SlotResupply'
import Button from 'components/Button'
import SlotIndicators from 'components/SlotIndicators'
import Steps from 'components/Steps'
import Loader from 'components/Loader'
import Error from 'components/Error'
// import Stomp from 'stompjs'
// import SockJS from 'sockjs-client'
// import { wssConfig } from '../../config'

const slotNames = [
    'leftSlot',
    'centerSlot',
    'rightSlot'
]

const ordinalToSlot = {
    0: 'leftSlot',
    1: 'centerSlot',
    2: 'rightSlot',
}

const resupplySteps = [
    {
        id: 'products',
        title: 'Put products in active slots',
        subtitle: 'or scan and put different products',
    },
    {
        id: 'quantity',
        title: 'Provide quantity',
        subtitle: 'or press confirm button',
    },
    {
        id: 'submit',
        title: 'Press submit to proceed',
    },
]

const initialSlot = {
    name: '',
    image_url: '',
    sku_id: '',
    quantity: 0,
    isConfirmed: false,
    disabled: true,
    supply_item_id: undefined,
    max_acc: 300
}

class SupplyTote extends React.Component {
    state = {
        tote_id: null,
        requestState: 'NONE',
        leftSlot: initialSlot,
        centerSlot: initialSlot,
        rightSlot: initialSlot,
        activeBarcodeInput: null,
    }

    componentDidMount() {
        const {location, history, failureUrl} = this.props
        this._isMounted = true

        const search = new URLSearchParams(location.search)
        const tote_id = search.get('id')

        if (tote_id == null) {
            history.push(failureUrl)
        } else {
            this.setState({tote_id}, () => this.beginRequest(this.fetchTote))
        }

        // Uncomment to enable barcode scanner via WS
        // this.websocketReconnectTimeout = setTimeout(() => this.initWebsocket(), 1000)
    }

    componentWillUnmount() {
        if (this.interval) {
            clearInterval(this.interval)
        }

        this._isMounted = false
        // Uncomment to enable barcode scanner via WS
        // this.closeWebsocket()
    }

    // Uncomment to enable barcode scanner via WS
    // initWebsocket = () => {
    //   this.sock = SockJS(wssConfig.baseUrl + 'operator-panel-websocket')
    //   this.stompClient = Stomp.over(this.sock)
    //
    //   this.stompClient.connect(
    //     {},
    //     frame => {
    //       this.stompSubscription = this.stompClient.subscribe('/topic/sku', res => {
    //         const sku = JSON.parse(res.body)
    //         if (sku.sku_id) {
    //           this.fetchItem(sku.sku_id)
    //         }
    //       })
    //     },
    //     this.errorWebsocketCallback,
    //   )
    //
    //   this.sock.onclose = this.errorWebsocketCallback
    // }
    //
    // errorWebsocketCallback = () => {
    //   this.closeWebsocket()
    //   this.websocketReconnectTimeout = setTimeout(() => this.initWebsocket(), 1000)
    // }
    //
    // closeWebsocket () {
    //   if (this.sock) {
    //     this.sock.close()
    //   }
    //
    //   if (this.websocketReconnectTimeout) {
    //     clearTimeout(this.websocketReconnectTimeout)
    //     this.websocketReconnectTimeout = null
    //   }
    //
    //   if (this.stompSubscription) {
    //     this.stompSubscription.unsubscribe()
    //   }
    //
    //   if (this.stompClient) {
    //     this.stompClient.disconnect()
    //   }
    // }

    beginRequest = request => {
        this.setState({requestState: 'LOADING'}, request)
    }

    pendingRequest = request => {
        this.setState({requestState: 'PENDING'}, request)
    }

    finishRequest = request => {
        this.setState({requestState: 'READY'}, request)
    }

    failRequest = request => {
        this.setState({requestState: 'ERROR'}, request)
    }

    // fetch information about tote
    fetchTote = () => {
        const {api, confirmation, history, t, supplyInfo, failureUrl} = this.props
        const {tote_id} = this.state
        api
            .toteDetails({tote_id})
            .then(res => {
                if (!this._isMounted) return
                const fullSlots = res.body.slots;
                const chosenSkus = supplyInfo.getCurrentProducts();
                const slots = fullSlots.reduce(this.slotsToObject, {})
                this.fillSlotsWithChosenSKU(chosenSkus, slots);
                this.fetchAllItems(slots)
            })
            .catch(err => {
                if (!this._isMounted) return
                const error = parseError(err)

        this.failRequest(() =>
          confirmation.openConfirmation({
            children: error.message,
            header: error.header,
            onConfirm: () => this.beginRequest(this.fetchTote),
            onCancel: () => history.push(failureUrl),
            confirmLabel: t('Retry'),
            type: 'error',
          }),
        )
      })
  }

    fillSlotsWithChosenSKU(chosenSkus, slots) {
        let i = 0;
        slotNames.forEach(value => {
            if (chosenSkus.length <= i) {
                console.log("Skipping as no more SKU were chosen!")
                return;
            }

            let chosenSku = chosenSkus[i];

            if (slots[value] === undefined) {
                console.log("Skipping slot: " + value + " as tote doesn't have it defined!")
                return;
            }

            if (slots[value].sku_id !== undefined && slots[value].sku_id !== null && slots[value].sku_id !== chosenSku.sku_id) {
                console.log("Skipping slot: " + value + " as this slot contains SKU: " + slots[value].sku_id)
                return;
            }

            slots[value].sku_id = chosenSku.sku_id;
            slots[value].item_id = chosenSku.item_id;
            i++;
        })
    }

    slotsToObject = (map, x) => {
        map[ordinalToSlot[x.displayOrdinal]] = {
            sku_id: x.sku_id,
            ordinal: x.ordinal,
            quantity: x.quantity
        };
        return map;
    }
    // Fetch all skus data for tote
    fetchAllItems = (slots) => {
        const {api, confirmation, t, history, failureUrl} = this.props
        let slotDetails = Object.getOwnPropertyNames(slots).map((key) =>
            this.skuDetailsPromise(api, slots, key).then(function(details) {
                    return {
                        name: key,
                        quantity: slots[key].quantity,
                        item_id: slots[key].item_id,
                        ordinal: slots[key].ordinal,
                        premise: details
                    }
                }
            )
        )
        Promise.all(slotDetails)
            .then(responses => {
                if (!this._isMounted) return

                // Parse slot array to object that matches initial state
                const newState = responses.reduce((acc, curr, index) => {
                    let prem = curr.premise;

                    acc[curr.name] = {
                        ...initialSlot,
                        disabled: false,
                        quantity: curr.quantity,
                        supply_item_id: curr.item_id,
                        ...prem?.body,
                        ordinal: curr.ordinal,
                    }
                    return acc
                }, {})
                this.setState({...newState, requestState: 'READY'})
            })
            .catch(err => {
                if (!this._isMounted) return
                const error = parseError(err)

        this.failRequest(() =>
          confirmation.openConfirmation({
            children: error.message,
            header: error.header,
            onConfirm: () => this.fetchAllItems(this.fetchTote),
            onCancel: () => history.push(failureUrl),
            confirmLabel: t('Retry'),
            type: 'error',
          }),
        )
      })
  }

    skuDetailsPromise(api, slots, key) {
        if(slots[key].sku_id === null) {
            return Promise.resolve();
        }
        return api.skuDetails({sku_id: slots[key].sku_id});
    }

// Handle request for single sku after barcode scan
  fetchItem = id => {
    const { api } = this.props
    const { activeBarcodeInput } = this.state

    api.skuDetails({ sku_id: id }).then(res => {
      const slotData = res.body
      if (slotData && activeBarcodeInput) {
        this.setState({ [activeBarcodeInput]: slotData })
      }
    })
  }

  onChange = (slot, field, value) => {
    if(field === 'sku_id') {
        this.fetchItem(value);
    }
    if (field === 'isConfirmed') {
      this.setState({ [slot]: { ...this.state[slot], [field]: value } })
    } else {
      this.setState({ [slot]: { ...this.state[slot], [field]: value, isConfirmed: false } })
    }
  }

  onBarcodeFocusChange = slot => {
    this.setState({ activeBarcodeInput: slot })
  }

  onSubmit = () => {
    const { api, history, confirmation, t, failureUrl, successUrl, location} = this.props
    const { leftSlot, centerSlot, rightSlot, tote_id } = this.state

    api
      .supplyToteInduct({ leftSlot, centerSlot, rightSlot, tote_id })
      .then(res => history.push(successUrl))
      .catch(err => {
        const error = parseError(err)

        this.finishRequest(() =>
          confirmation.openConfirmation({
            children: error.message,
            header: error.header,
            onConfirm: () => this.pendingRequest(this.onSubmit),
            onCancel: () => history.push(location.pathname),
            confirmLabel: t('Retry'),
            type: 'error',
          }),
        )
      })
  }

  render () {
    const { t } = this.props
    const { leftSlot, centerSlot, rightSlot, requestState } = this.state
    // Check whether tote can be submitted
    const isSubmitDisabled =
      !(
        (leftSlot.isConfirmed || leftSlot.disabled) &&
        (rightSlot.isConfirmed || rightSlot.disabled) &&
        (centerSlot.isConfirmed || centerSlot.disabled)
      ) || requestState === 'PENDING'

    if (requestState === 'LOADING') {
      return <Loader />
    }

    if (requestState === 'ERROR') {
      return <Error />
    }

    return (
      <div className='rcs-tote-resupply'>
        {requestState === 'LOADING' ? (
          <Loader />
        ) : (
          <>
            <Steps
              steps={resupplySteps}
              active={isSubmitDisabled ? ['products', 'quantity'] : ['submit']}
            />
            <div className='rcs-tote-resupply__content'>
              <SlotResupply
                {...leftSlot}
                slotName='leftSlot'
                onChange={this.onChange.bind(this, 'leftSlot')}
                onBarcodeFocusChange={this.onBarcodeFocusChange}
              />
              <SlotResupply
                {...centerSlot}
                slotName='centerSlot'
                onChange={this.onChange.bind(this, 'centerSlot')}
                onBarcodeFocusChange={this.onBarcodeFocusChange}
              />
              <SlotResupply
                {...rightSlot}
                slotName='rightSlot'
                onChange={this.onChange.bind(this, 'rightSlot')}
                onBarcodeFocusChange={this.onBarcodeFocusChange}
              />
            </div>
            <Button
              className='rcs-tote-resupply__submit'
              disabled={isSubmitDisabled}
              btnType='accent'
              onClick={this.onSubmit}
            >
              {t('Submit')}
            </Button>
            <SlotIndicators
              className='rcs-tote-resupply__slots'
              hasCenter={!centerSlot.disabled}
              hasLeft={!leftSlot.disabled}
              hasRight={!rightSlot.disabled}
            />
          </>
        )}
      </div>
    )
  }
}

export default compose(
  withTranslation(),
  withSupplyInfo,
  withApi,
  withRouter,
  withConfirmation,
)(SupplyTote)
