import * as yup from 'yup'

export const Tote = yup
  .object()
  .shape({
    tote_id: yup.string().nullable(),
    slots: yup
      .array()
      .of(
        yup.object().shape({
          ordinal: yup.number().nullable(),
          display_ordinal: yup.number().nullable(),
          storage_inventory: yup
            .object()
            .shape({
              available: yup.number().nullable(),
              reservations: yup
                .array()
                .of(
                  yup
                    .object()
                    .shape({
                      order_id: yup.string().nullable(),
                      quantity: yup.number().nullable(),
                    })
                    .nullable(),
                )
                .nullable(),
              sku_batch: yup
                .object()
                .shape({
                  // Nullbable for development
                  sku_id: yup.string().nullable(),
                  quantity: yup.number().nullable(),
                  sell_by_date: yup.string().nullable(),
                  state: yup.string().nullable(),
                })
                .nullable(),
            })
            .nullable(),
        }),
      )
      .nullable(),
    tote_type: yup.object().nullable(),
    tote_orientation: yup.string().nullable(),
    tote_status: yup.string().nullable(),
  })
  .nullable()
  .required()

export const ToteList = yup
  .object()
  .shape({
    totes: yup
      .array()
      .of(Tote)
      .nullable(),
  })
  .required()
