import React from 'react'
import { useTranslation } from 'react-i18next'
import PropTypes from 'prop-types'

import Input from 'components/Input'

const Slot = props => {
  const { data, index, onChange } = props
  const { t } = useTranslation()
  const reservations = data && data.reservations

  function handleChange (e) {
    const { name, value } = e.target
    onChange(data.ordinal, name, value)
  }

  return (
    <div className='rcs-tote-edit-slot'>
      {data ? (
        <>
          <div className='rcs-tote-edit-slot__title'>
            {t('Slot')} {index}
          </div>
          <div className='rcs-tote-edit-slot__labels'>
            <div className='rcs-tote-edit-slot__label'>{t('SKU')}</div>
            <div className='rcs-tote-edit-slot__label'>{t('Quantity')}</div>
            <div className='rcs-tote-edit-slot__label'>{t('Available')}</div>
            <div className='rcs-tote-edit-slot__label'>{t('Reserved')}</div>
            <div className='rcs-tote-edit-slot__label'>{t('Reservations')}</div>
          </div>
          <div className='rcs-tote-edit-slot__form'>
            <Input name='sku_id' value={data.sku_id} onChange={handleChange} />
            <Input name='quantity' value={data.quantity} onChange={handleChange} />
            <Input name='available' value={data.available} onChange={handleChange} />
            <div className='rcs-tote-edit-slot__infobox'>{data.reservationCount}</div>
            {reservations.map(rsv => (
              <div key={rsv.order_id} className='rcs-tote-edit-slot__infobox'>
                {rsv.quantity} {t('by Order')} {rsv.order_id}
              </div>
            ))}
          </div>
        </>
      ) : (
        <>
          <div className='rcs-tote-edit-slot__title'>
            {t('Slot')} {index}
          </div>
          <div></div>
          <div className='rcs-tote-edit-slot__form rcs-tote-edit-slot__empty'>{t('Empty')}</div>
        </>
      )}
    </div>
  )
}

Slot.propTypes = {
  data: PropTypes.shape({
    sku_id: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
    quantity: PropTypes.number,
    available: PropTypes.number,
    reservations: PropTypes.arrayOf(
      PropTypes.shape({
        order_id: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
        quantity: PropTypes.number,
      }),
    ),
  }),
}

export default Slot
