import React from 'react'
import PropTypes from 'prop-types'
import cs from 'classnames'

import { ReactComponent as Marker } from 'assets/icons/marker.svg'

const SlotIndicators = props => {
  const { className, hasCenter, hasLeft, hasRight } = props
  const wrapperCls = cs(className, 'rcs-slot-indicators')

  return (
    <div className={wrapperCls}>
      <div className='rcs-slot-indicators__marker'>{hasLeft && <Marker />}</div>
      <div className='rcs-slot-indicators__marker'>{hasCenter && <Marker />}</div>
      <div className='rcs-slot-indicators__marker'>{hasRight && <Marker />}</div>
    </div>
  )
}

SlotIndicators.propTypes = {
  hasLeft: PropTypes.bool,
  hasCenter: PropTypes.bool,
  hasRight: PropTypes.bool,
}

export default SlotIndicators
