import React from 'react'

import Loader from 'components/Loader'
import ProgressBar from 'components/ProgressBar'
import {toteTimeToArrive} from 'config'
import {compose} from "recompose";
import {withTranslation} from "react-i18next";
import {withApi, withSupplyInfo} from "../../providers";

class WaitForTote extends React.Component {
    state = {
        timeLeft: toteTimeToArrive,
        currentProducts: this.props.supplyInfo.getCurrentProducts()
    }

    componentDidMount () {
        if (toteTimeToArrive) {
            this.interval = setInterval(this.tickHandler, 1000)
        }
    }

    componentWillUnmount () {
        if (this.interval) {
            clearInterval(this.interval)
        }
    }

    tickHandler = () => {
        const { timeLeft } = this.state
        if (timeLeft <= 1 && this.interval) {
            clearInterval(this.interval)
        }
        this.setState(prevState => ({ timeLeft: prevState.timeLeft - 1 }))
    }

    render() {
        const { currentProducts, timeLeft } = this.state
        const { t } = this.props

        const loader = toteTimeToArrive ? (
            <ProgressBar
                timeLeft={timeLeft}
                timeEstimation={toteTimeToArrive}
                label={t('{{time}}s remaining', { time: timeLeft })}
            />
        ) : (
            <Loader />
        )

        return (
            <div className='rcs-supply-loading'>
                <div className='rcs-supply-loading__header'>
                    {t('Tote is on the way to the loading gate.')}
                </div>
                <div className='rcs-supply-loading__products'>
                    <h3>{t('Please prepare')}</h3>
                    <div className='rcs-supply-loading__products-line'>
                        {currentProducts.map(x => (
                            <div key={x.item_id} className='rcs-supply-loading__item'>
                                {x.sku_name || x.sku_id}
                            </div>
                        ))}
                    </div>
                </div>
                <div>{loader}</div>
            </div>
        )
    }
}

export default compose(
    withTranslation(),
    withApi,
    withSupplyInfo)(WaitForTote)