import React from 'react'
import { motion } from 'framer-motion'
import cs from 'classnames'

export const MountTransition = ({ children, slide = 0, slideUp = 0, className, transition }) => (
  <motion.div
    exit={{ opacity: 0, x: slide, y: slideUp }}
    initial={{ opacity: 0, x: slide, y: slideUp }}
    animate={{ opacity: 1, x: 0, y: 0 }}
    transition={{ duration: 0.15, ...transition }}
    className={cs(className, 'rcs-animated-route')}
  >
    {children}
  </motion.div>
)

export default MountTransition
