import React from 'react'
import { Redirect } from 'react-router-dom'

import AnimatedRoute from 'components/AnimatedRoute'
import AnimatedSwitch from 'components/AnimatedSwitch'
import Orders from './Development/Orders'
import Navbar from './Navbar'
import ToteEdit from './Development/ToteEdit'
import Totes from './Development/Totes'

import { developmentPaths } from 'config'

const Development = () => {
  return (
    <>
      <Navbar isSubnav paths={developmentPaths} />
      <AnimatedSwitch>
        <AnimatedRoute path='/development/orders'>
          <Orders />
        </AnimatedRoute>
        <AnimatedRoute path='/development/totes/:id'>
          <ToteEdit />
        </AnimatedRoute>
        <AnimatedRoute exact path='/development/totes'>
          <Totes />
        </AnimatedRoute>
        <AnimatedRoute path='/development/sku'>
          <div>SKU</div>
        </AnimatedRoute>
        <AnimatedRoute path='/development/sm-support'>
          <div>SM-SUPPORT</div>
        </AnimatedRoute>
        <AnimatedRoute path='/development*'>
          <Redirect to='/development/orders' />
        </AnimatedRoute>
      </AnimatedSwitch>
    </>
  )
}

export default Development
