import React from 'react'
import PropTypes from 'prop-types'
import { useTranslation } from 'react-i18next'
import cs from 'classnames'

import { ReactComponent as Active } from 'assets/icons/active.svg'
import { ReactComponent as Done } from 'assets/icons/done.svg'
import { ReactComponent as Todo } from 'assets/icons/to_do.svg'

function getStepCls (index, minActiveIndex, maxActiveIndex) {
  let state = 'active'

  if (index < minActiveIndex) {
    state = 'done'
  }
  if (index > maxActiveIndex) {
    state = 'todo'
  }

  return cs('rcs-steps__step', `rcs-steps__step--${state}`)
}

function getSubtitle (index, minActiveIndex, maxActiveIndex) {
  if (index > maxActiveIndex) {
    return 'To do'
  }
  if (index < minActiveIndex) {
    return 'Completed'
  }
  return 'In progress'
}

const Steps = props => {
  const { steps = [], active = [] } = props
  const { t } = useTranslation()
  const activeIndexes = steps
    .map((x, index) => (active.indexOf(x.id) > -1 ? index : undefined))
    .filter(x => !isNaN(x))
  const minActiveIndex = Math.min(...activeIndexes)
  const maxActiveIndex = Math.max(...activeIndexes)

  return (
    <div className='rcs-steps'>
      {steps.map((x, index) => (
        <div key={x.title} className={getStepCls(index, minActiveIndex, maxActiveIndex)}>
          <h4 className='rcs-steps__step-title'>{t(x.title)}</h4>
          <div className='rcs-steps__step-subtitle'>
            {t(x.subtitle || getSubtitle(index, minActiveIndex, maxActiveIndex))}
          </div>
          <Active className='rcs-steps__step-icon rcs-steps__step-icon--active' />
          <Done className='rcs-steps__step-icon rcs-steps__step-icon--done' />
          <Todo className='rcs-steps__step-icon rcs-steps__step-icon--todo' />
        </div>
      ))}
    </div>
  )
}

Steps.propTypes = {
  steps: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.string,
      title: PropTypes.string,
      subtitle: PropTypes.string,
    }),
  ),
  active: PropTypes.array,
}

export default Steps
