import React from 'react'
import PropTypes from 'prop-types'
import cs from 'classnames'

import { Cell } from 'components/Table'

const SlotCell = props => {
  const { data, className, ...restProps } = props
  const wrapperCls = cs(className, 'rcs-totes__slot-cell', { 'rcs-totes__slot-cell--empty': !data })

  return (
    <Cell className={wrapperCls} {...restProps}>
      {data ? (
        <>
          <div>SKU: {data.sku_id}</div>
          <div>Quantity: {data.quantity}</div>
          <div>Available: {data.available}</div>
          <div className='rcs-totes__slot-cell-reservations'>
            Reserved:
            {data.reservations.map(item => (
              <React.Fragment key={item.order_id}>
                {item.quantity} ({item.order_id}),
              </React.Fragment>
            ))}
          </div>
        </>
      ) : (
        <div>Empty</div>
      )}
    </Cell>
  )
}

SlotCell.propTypes = {
  data: PropTypes.shape({
    sku_id: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
    quantity: PropTypes.number,
    available: PropTypes.number,
    reservations: PropTypes.arrayOf(
      PropTypes.shape({
        order_id: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
        quantity: PropTypes.number,
      }),
    ),
  }),
}

export default SlotCell
