import React, { useContext } from 'react'
import { withTranslation } from 'react-i18next'

import { getDeliveryDisplayName } from '../helpers'
import { deliveryStoragePrefix } from 'config'

const SupplyInfoContext = React.createContext()

class SupplyInfoProvider extends React.PureComponent {
  state = {
    delivery: localStorage.getItem(deliveryStoragePrefix)
      ? JSON.parse(localStorage.getItem(deliveryStoragePrefix))
      : null,
    currentProducts: [],
  }

  getDelivery = () => {
    const { t } = this.props
    const { delivery } = this.state

    if (delivery) {
      return { displayName: getDeliveryDisplayName(delivery, t), ...delivery }
    }

    return delivery
  }

  setDelivery = delivery => {
    if (delivery) {
      localStorage.setItem(deliveryStoragePrefix, JSON.stringify(delivery))
      this.setState({ delivery })
    }
  }

  resetDelivery = () => {
    localStorage.removeItem(deliveryStoragePrefix)
    this.setState({ delivery: null })
  }

  getCurrentProducts = () => {
    return this.state.currentProducts
  }

  setCurrentProducts = (currentProducts, callback) => {
    if (Array.isArray(currentProducts)) {
      this.setState({ currentProducts }, callback)
    }
  }

  resetCurrentProducts = currentProducts => {
    this.setState({ currentProducts: [] })
  }

  render () {
    const { children } = this.props
    const supplyInfo = {
      getDelivery: this.getDelivery,
      setDelivery: this.setDelivery,
      resetDelivery: this.resetDelivery,

      getCurrentProducts: this.getCurrentProducts,
      setCurrentProducts: this.setCurrentProducts,
      resetCurrentProducts: this.resetCurrentProducts,
    }
    return <SupplyInfoContext.Provider value={supplyInfo}>{children}</SupplyInfoContext.Provider>
  }
}

const withSupplyInfo = Component => props => (
  <SupplyInfoContext.Consumer>
    {supplyInfo => <Component supplyInfo={supplyInfo} {...props} />}
  </SupplyInfoContext.Consumer>
)

const useSupplyInfo = () => {
  const supplyInfo = useContext(SupplyInfoContext)
  return { supplyInfo }
}

const SupplyInfoProviderWithTranslations = withTranslation()(SupplyInfoProvider)

export { SupplyInfoProviderWithTranslations as SupplyInfoProvider, withSupplyInfo, useSupplyInfo }
