import i18next from 'i18next/index'
import { initReactI18next } from 'react-i18next'
import Backend from 'i18next-xhr-backend'

i18next
  .use(initReactI18next)
  .use(Backend)
  .init({
    lng: 'en',
    fallbackLng: 'en',
    keySeparator: false,
    backend: {
      loadPath: '/translations/{{lng}}.json',
    },
    react: {
      useSuspense: false,
    },
  })
