import React, { useEffect, useRef, useState } from 'react'
import PropTypes from 'prop-types'
import cs from 'classnames'

import { ReactComponent as Arrow } from 'assets/icons/arrow.svg'

const itemToStringDefault = x => (x && typeof x === 'object' ? x.label : x)
const itemToValueDefault = x => x

const Select = props => {
  const {
    data,
    value,
    placeholder,
    onSelect,
    itemToString = itemToStringDefault,
    itemToValue = itemToValueDefault,
    readOnly,
  } = props

  const [isOpen, setIsOpen] = useState(false)
  const ref = useRef()

  const wrapperCls = cs('rcs-select', { 'rcs-select--open': isOpen })
  const menuCls = cs('rcs-select__menu', { 'rcs-select__menu--open': isOpen })
  const arrowCls = cs('rcs-select__arrow', { 'rcs-select__arrow--open': isOpen })

  const selectHandler = item => {
    if (onSelect) {
      const nextValue = itemToValue(item)
      onSelect(nextValue)
    }
    setIsOpen(false)
  }

  const clickHandler = e => {
    if (ref.current && !ref.current.contains(e.target) && isOpen) {
      setIsOpen(false)
    }
  }

  const openHandler = () => {
    if (!readOnly) {
      setIsOpen(!isOpen)
    }
  }

  useEffect(() => {
    document.addEventListener('click', clickHandler)
    return () => document.removeEventListener('click', clickHandler)
  })

  return (
    <div className={wrapperCls}>
      <div onClick={openHandler} className='rcs-select__button'>
        <div className='rcs-select__label'>
          {value ? (
            itemToString(value)
          ) : (
            <span className='rcs-select__placeholder'>{placeholder}</span>
          )}
        </div>
        <Arrow className={arrowCls} />
      </div>

      {isOpen && data && (
        <div className={menuCls} ref={ref}>
          {data.map(item => (
            <div
              key={itemToString(item)}
              className='rcs-select__menu-item'
              onClick={() => selectHandler(item)}
            >
              {itemToString(item)}
            </div>
          ))}
        </div>
      )}
    </div>
  )
}

Select.propTypes = {
  data: PropTypes.arrayOf(PropTypes.oneOfType([PropTypes.string, PropTypes.object])),
  value: PropTypes.any,
  placeholder: PropTypes.string,
  onSelect: PropTypes.func,
  itemToString: PropTypes.func,
  itemToValue: PropTypes.func,
  readOnly: PropTypes.bool,
}

export default Select
