import React from 'react'
import PropTypes from 'prop-types'

const Head = props => {
  const { headers = [], withActions = true } = props

  return (
    <thead className='rcs-table-head'>
      <tr>
        {headers.map(header => (
          <th key={header.id}>
            <div className='rcs-table-head__cell'>{header.label}</div>
          </th>
        ))}
        {withActions && <th></th>}
      </tr>
    </thead>
  )
}

Head.propTypes = {
  headers: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.string,
      label: PropTypes.node,
    }),
  ),
  withActions: PropTypes.bool,
}

export default Head
