import ApiClient from './ApiClient'
import * as models from './models'

import { parseInductTote, parseTotes } from 'helpers'

export default class UserApi {
  constructor (config) {
    this.apiClient = new ApiClient(config)
    this.config = config
  }

  // USER HANDLERS
  userLogin (data) {
    let path = '/user:authenticate'
    if (this.config.externalPrefix) {
      path = '/' + this.config.externalPrefix + path
    }
    return this.apiClient.post(path, data)
  }

  // FILTERS
  filtersList (query) {
    let path = '/filter:list'
    if (this.config.externalPrefix) {
      path = '/' + this.config.externalPrefix + path
    }
    return this.apiClient.get(path, query)
  }

  filtersCreate (body) {
    let path = '/filter:create'
    if (this.config.externalPrefix) {
      path = '/' + this.config.externalPrefix + path
    }
    return this.apiClient.post(path, body)
  }

  filtersUpdate (body) {
    let path = '/filter:update'
    if (this.config.externalPrefix) {
      path = '/' + this.config.externalPrefix + path
    }
    return this.apiClient.post(path, body)
  }

  filtersDelete (body) {
    let path = '/filter:delete'
    if (this.config.externalPrefix) {
      path = '/' + this.config.externalPrefix + path
    }
    return this.apiClient.post(path, body)
  }

  // TASK HANDLERS
  issueList (query) {
    let path = '/issue:list'
    if (this.config.internalPrefix) {
      path = '/' + this.config.internalPrefix + path
    }
    return this.apiClient.get(path, query, models.IssueList)
  }

  issueDetails (query) {
    let path = '/issue:details'
    if (this.config.internalPrefix) {
      path = '/' + this.config.internalPrefix + path
    }
    return this.apiClient.get(path, query, models.Issue)
  }

  issueStart (body) {
    let path = '/issue:start'
    if (this.config.internalPrefix) {
      path = '/' + this.config.internalPrefix + path
    }
    return this.apiClient.post(path, body)
  }

  issueClose (body) {
    let path = '/issue:close'
    if (this.config.internalPrefix) {
      path = '/' + this.config.internalPrefix + path
    }
    return this.apiClient.post(path, body)
  }

  // ORDERS HANLDERS
  orderList (query) {
    let path = '/order:list'
    if (this.config.externalPrefix) {
      path = '/' + this.config.externalPrefix + path
    }
    return this.apiClient.get(path, query, models.OrderList)
  }

  orderResend (body) {
    let path = '/order:resend'
    if (this.config.externalPrefix) {
      path = '/' + this.config.externalPrefix + path
    }
    return this.apiClient.post(path, body)
  }

  orderDelete (body) {
    let path = '/order:delete'
    if (this.config.externalPrefix) {
      path = '/' + this.config.externalPrefix + path
    }
    return this.apiClient.post(path, body)
  }

  // TOTES HANDLERS
  toteList (query) {
    query = this.apiClient.filterEmptyQueryParms(query)
    let path = '/tote:list'
    if (this.config.internalPrefix) {
      path = '/' + this.config.internalPrefix + path
    }
    return this.apiClient.get(path, query, models.ToteList).then(res => {
      res.body.totes = Array.isArray(res.body.totes) ? res.body.totes.map(parseTotes) : []
      return res
    })
  }

  toteDetails (query) {
    query = this.apiClient.filterEmptyQueryParms(query)
    let path = '/tote:details'
    if (this.config.internalPrefix) {
      path = '/' + this.config.internalPrefix + path
    }
    return this.apiClient.get(path, query, models.Tote).then(res => {
      res.body = parseTotes(res.body)
      return res
    })
  }

  toteUpdate (body) {
    let path = '/tote:update'
    if (this.config.internalPrefix) {
      path = '/' + this.config.internalPrefix + path
    }
    return this.apiClient.post(path, body)
  }

  toteDelete (body) {
    let path = '/tote:delete'
    if (this.config.internalPrefix) {
      path = '/' + this.config.internalPrefix + path
    }
    return this.apiClient.post(path, body)
  }

  toteEject (body) {
    let path = '/tote:summon'
    if (this.config.internalPrefix) {
      path = '/' + this.config.internalPrefix + path
    }
    return this.apiClient.post(path, body)
  }

  toteDisable (body) {
    let path = '/tote:disable'
    if (this.config.internalPrefix) {
      path = '/' + this.config.internalPrefix + path
    }
    return this.apiClient.post(path, body)
  }

  // SKU
  skuDetails (query) {
    let path = '/sku:details'
    if (this.config.externalPrefix) {
      path = '/' + this.config.externalPrefix + path
    }
    return this.apiClient.get(path, query)
  }

  // DELIVERIES
  supplyList (query) {
    let path = '/supply:list'
    if (this.config.internalPrefix) {
      path = '/' + this.config.internalPrefix + path
    }
    return this.apiClient.get(path, query, models.SupplyList)
  }

  supplyItemsList (query) {
    let path = '/supply_items:list'
    if (this.config.internalPrefix) {
      path = '/' + this.config.internalPrefix + path
    }
    return this.apiClient.get(path, query, models.SupplyItems)
  }

  supplyRequestTote (body) {
    let path = '/supply:request_tote'
    if (this.config.internalPrefix) {
      path = '/' + this.config.internalPrefix + path
    }
    return this.apiClient.post(path, body, models.SupplyList)
  }

  supplyToteInduct (body) {
    let path = '/tote:induct'
    if (this.config.internalPrefix) {
      path = '/' + this.config.internalPrefix + path
    }

    body = parseInductTote(body)

    return this.apiClient.post(path, body)
  }

  openGate () {
    let path = '/loading-gate:open'
    if(this.config.internalPrefix) {
      path = '/' + this.config.internalPrefix + path
    }
    return this.apiClient.post(path, {});
  }
}
