import React, { useState } from 'react'
import PropTypes from 'prop-types'
import cs from 'classnames'

import Badge from 'components/Badge'
import { Cell } from 'components/Table'
import { ReactComponent as Arrow } from 'assets/icons/arrow.svg'

const OrderInfoCell = props => {
  const { data = [] } = props
  const [isOpen, setIsOpen] = useState(false)
  const displayedData = !isOpen ? data.slice(0, 1) : data
  const isExpandable = data.length > 1

  const wrapperCls = cs('rcs-orders__order-info-cell', {
    'rcs-orders__order-info-cell--expandable': isExpandable,
  })
  const arrowCls = cs('rcs-orders__order-info-arrow', {
    'rcs-orders__order-info-arrow--open': isOpen,
  })

  return (
    <Cell className={wrapperCls}>
      {displayedData.map((item, index) => (
        <div key={item.sku_id} className='rcs-orders__order-info-item'>
          <div className='rcs-orders__order-info-sku'>
            <span>{index === 0 && 'SKU:'}</span>
            {item.sku_id}
          </div>
          <div>
            <Badge type='info'>{item.quantity}</Badge>
          </div>
          <div>
            <Badge type='success'>{item.picked}</Badge>
          </div>
          <div>
            <Badge type='warn'>{item.failed}</Badge>
          </div>
          <div className='rcs-orders__order-info-tote'>
            {index === 0 && <span>Storage Tote:</span>} {item.storage_tote}
          </div>
          {isExpandable && (
            <div className={arrowCls} onClick={() => setIsOpen(!isOpen)}>
              <Arrow />
            </div>
          )}
        </div>
      ))}
    </Cell>
  )
}

OrderInfoCell.propTypes = {
  date: PropTypes.arrayOf(
    PropTypes.shape({
      sku_id: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
      quantity: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
      picked: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
      failed: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
      storage_tote: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    }),
  ),
}

export default OrderInfoCell
