import React from 'react'
import { compose } from 'recompose'
import { withTranslation } from 'react-i18next'

import { withApi, withConfirmation } from 'providers'
import { createFilterName, parseError } from 'helpers'

import Button from 'components/Button'
import ContentWrapper from 'components/ContentWrapper'
import Loader from 'components/Loader'
import Filters from './Totes/Filters'
import TotesList from './Totes/TotesList'

const initialFilters = {
  height: '',
  order_id: '',
  partitioning: '',
  sku: '',
  status: '',
  tote_id: '',
  only_in_progress: false,
}

class Totes extends React.Component {
  state = {
    totes: [],
    selectedItems: [],
    requestState: 'LOADING',
    filters: initialFilters,
    presets: [],
  }

  _isMounted = false

  componentDidMount () {
    this._isMounted = true
    const urlParams = new URLSearchParams(window.location.search)
    const orderId = urlParams.get('orderId')

    this.presetsFetch()
    this.beginRequest(this.totesFetch)
    if (orderId) {
      this.setState({
        filters: {
          orderId,
        },
      })
    }
  }
  componentWillUnmount () {
    this._isMounted = false
  }

  beginRequest = request => {
    if (!this._isMounted) return
    this.setState({ requestState: 'LOADING' }, request)
  }

  failRequest = request => {
    if (!this._isMounted) return
    this.setState({ requestState: 'ERROR' }, request)
  }

  totesFetch = () => {
    const { api, confirmation, t } = this.props
    const { filters } = this.state

    api
      .toteList(filters)
      .then(res => {
        if (!this._isMounted) return
        this.setState(() => {
          return { totes: res.body.totes, requestState: 'READY', selectedItems: [] }
        })
      })
      .catch(err => {
        if (!this._isMounted) return
        const error = parseError(err)

        this.failRequest(() =>
          confirmation.openConfirmation({
            children: error.message,
            header: error.header,
            onConfirm: () => this.beginRequest(this.totesFetch),
            confirmLabel: t('Retry'),
            type: 'error',
          }),
        )
      })
  }

  presetsFetch () {
    const { api } = this.props

    api.filtersList({ target: 'TOTE' }).then(res => {
      if (!this._isMounted) return
      this.setState({ presets: res.body.filters })
    })
  }

  onFiltersChange = filters => {
    this.setState({ filters })
  }

  onPresetSelect = preset => {
    this.setState({ filters: { ...initialFilters, ...preset.fields } })
  }

  onPresetSave = () => {
    const { api } = this.props
    const { filters } = this.state
    const filterName = createFilterName(filters)

    const body = {
      fields: filters,
      target: 'TOTE',
      filter_id: filterName,
    }

    api.filtersCreate(body).then(() => this.presetsFetch())
  }

  onItemSelect = (id, selected) => {
    const { selectedItems } = this.state
    const nextSelectedItems = selectedItems.filter(x => x !== id)

    if (selected) {
      nextSelectedItems.push(id)
    }

    this.setState({ selectedItems: nextSelectedItems })
  }

  onItemSelectAll = e => {
    const { checked } = e.target

    if (checked) {
      const { totes } = this.state
      const nextSelectedItems = totes.map(x => x.tote_id)
      this.setState({ selectedItems: nextSelectedItems })
    } else {
      this.setState({ selectedItems: [] })
    }
  }

  totesDelete = ids => {
    const { api } = this.props
    if (Array.isArray(ids) && ids.length > 0) {
      api.toteDelete({ totes_id: ids }).then(() => this.totesFetch())
    }
  }

  totesDisable = ids => {
    const { api } = this.props
    if (Array.isArray(ids) && ids.length > 0) {
      api.toteDisable({ totes_id: ids }).then(() => this.totesFetch())
    }
  }

  totesEject = ids => {
    const { api } = this.props
    if (Array.isArray(ids) && ids.length > 0) {
      api.toteEject({ totes_id: ids }).then(() => this.totesFetch())
    }
  }

  render () {
    const { t } = this.props
    const { totes, requestState, filters, selectedItems, presets } = this.state
    const isBtnDisabled = selectedItems.length < 1

    return (
      <ContentWrapper className='rcs-totes'>
        <div className='rcs-totes__filters'>
          <Filters
            onChange={this.onFiltersChange}
            onApply={this.totesFetch}
            onPresetSave={this.onPresetSave}
            onPresetSelect={this.onPresetSelect}
            values={filters}
            presets={presets}
          />
        </div>
        {requestState === 'LOADING' ? (
          <Loader />
        ) : (
          <>
            <div className='rcs-totes__actions'>
              <Button
                disabled={isBtnDisabled}
                onClick={() => this.totesDelete(selectedItems)}
                btnType='secondary'
              >
                {t('Delete')}
              </Button>
              <Button disabled={isBtnDisabled} onClick={() => this.totesDisable(selectedItems)}>
                {t('Disable')}
              </Button>
              <Button disabled={isBtnDisabled} onClick={() => this.totesEject(selectedItems)}>
                {t('Eject')}
              </Button>
            </div>

            <div className='rcs-totes__table'>
              <TotesList
                totes={totes}
                onItemDelete={this.totesDelete}
                onItemDisable={this.totesDisable}
                onItemSelect={this.onItemSelect}
                onItemSelectAll={this.onItemSelectAll}
                selectedItems={selectedItems}
              />
            </div>
          </>
        )}
      </ContentWrapper>
    )
  }
}

export default compose(withTranslation(), withApi, withConfirmation)(Totes)
