import React from 'react'
import { useHistory } from 'react-router-dom'
import { useTranslation } from 'react-i18next'

import Button from 'components/Button'
import { Table, Head, Row, Cell } from 'components/Table'
import SlotCell from './SlotCell'
import Checkbox from '../../../components/Checkbox'
import { arraysEqual } from 'helpers'

const getSlotInfo = (expectedOrdinal, slots) => {
  return slots.find(x => x.ordinal === expectedOrdinal)
}

const TotesList = props => {
  const { totes, onItemSelect, selectedItems, onItemSelectAll, onItemDelete, onItemDisable } = props
  const { t } = useTranslation()
  const history = useHistory()

  const allChecked = arraysEqual(
    totes.map(x => x.tote_id),
    selectedItems,
  ) && selectedItems.length > 0

  const toteHeaders = [
    { id: 'select_all', label: <Checkbox onChange={onItemSelectAll} checked={allChecked} /> },
    { id: 'tote_id', label: 'Tote ID' },
    { id: 'slot_1', label: 'Slot 1' },
    { id: 'slot_2', label: 'Slot 2' },
    { id: 'slot_3', label: 'Slot 3' },
    { id: 'orientation', label: 'Orientation' },
    { id: 'height', label: 'Height' },
    { id: 'partitioning', label: 'Partitioning' },
    { id: 'status', label: 'Status' },
  ]

  const onSelect = e => {
    const { checked, name } = e.target
    onItemSelect(name, checked)
  }

  const handleEdit = id => {
    history.push(`/development/totes/${id}`)
  }

  return (
    <Table className='rcs-totes__table' head={<Head headers={toteHeaders} />}>
      {totes.map(tote => (
        <Row key={tote.tote_id}>
          <Cell>
            <Checkbox
              checked={selectedItems.indexOf(tote.tote_id) > -1}
              onChange={onSelect}
              name={tote.tote_id}
            />
          </Cell>
          <Cell>{tote.tote_id}</Cell>
          <SlotCell data={getSlotInfo(0, tote.slots)} />
          <SlotCell data={getSlotInfo(1, tote.slots)} />
          <SlotCell data={getSlotInfo(2, tote.slots)} />
          <Cell>{tote.tote_orientation}</Cell>
          <Cell>{tote.tote_height}</Cell>
          <Cell>{tote.tote_partitioning}</Cell>
          <Cell>{tote.tote_status}</Cell>
          <Cell className='rcs-totes__table-actions-cell'>
            <Button onClick={() => onItemDelete([tote.tote_id])} btnType='table'>
              {t('Delete')}
            </Button>
            <Button onClick={() => onItemDisable([tote.tote_id])} btnType='table'>
              {t('Disable')}
            </Button>
            <Button onClick={() => handleEdit(tote.tote_id)} btnType='table'>
              {t('Edit')}
            </Button>
          </Cell>
        </Row>
      ))}
    </Table>
  )
}

export default TotesList
