import React from 'react'
import { compose } from 'recompose'
import { withRouter } from 'react-router'
import { withTranslation } from 'react-i18next'

import { withApi, withConfirmation } from 'providers'
import { parseError } from 'helpers'

import Badge from 'components/Badge'
import Button from 'components/Button'
import ContentWrapper from 'components/ContentWrapper'
import Loader from 'components/Loader'
import { Table, Cell, Row, Head } from 'components/Table'

const headers = [
  { id: 'id', label: 'ID' },
  { id: 'action', label: 'Action' },
  { id: 'deadline', label: 'Deadline' },
  { id: 'status', label: 'Status' },
  { id: 'reason', label: 'Reason' },
]

class IssuesList extends React.Component {
  state = {
    issues: [],
    requestState: 'LOADING',
  }

  componentDidMount () {
    this._isMounted = true
    this.beginRequest(this.issuesLoad)
  }

  componentWillUnmount () {
    this._isMounted = true
  }

  beginRequest = request => {
    if (!this._isMounted) return
    this.setState({ requestState: 'LOADING' }, request)
  }

  failRequest = request => {
    if (!this._isMounted) return
    this.setState({ requestState: 'ERROR' }, request)
  }

  issuesLoad () {
    const { api, t, confirmation } = this.props

    api
      .issueList()
      .then(res => {
        if (!this._isMounted) return
        this.setState({ issues: res.body.issues, requestState: 'READY' })
      })
      .catch(err => {
        if (!this._isMounted) return
        const error = parseError(err)

        this.failRequest(
          confirmation.openConfirmation({
            children: error.message,
            header: error.header,
            onConfirm: () => this.beginRequest(this.issuesLoad),
            confirmLabel: t('Retry'),
            type: 'error',
          }),
        )
      })
  }

  issueStart = id => {
    const { api, t, confirmation } = this.props
    api
      .issueStart({ issue_id: id })
      .then(this.issuesLoad)
      .catch(err => {
        if (!this._isMounted) return
        const error = parseError(err)

        this.failRequest(
          confirmation.openConfirmation({
            children: error.message,
            header: error.header,
            onConfirm: this.issueStart(),
            confirmLabel: t('Retry'),
            type: 'error',
          }),
        )
      })
  }

  onIssuePreview = id => {
    const { history } = this.props
    if (id) {
      history.push(`/issues/start/${id}`)
    }
  }

  render () {
    const { t } = this.props
    const { issues, requestState } = this.state

    return (
      <ContentWrapper className='rcs-issues-list'>
        <h2>{t('Issues list')}</h2>
        <div className='rcs-issues-list__wrapper'>
          {requestState === 'LOADING' ? (
            <Loader />
          ) : (
            <Table head={<Head headers={headers} />}>
              {issues.map(issue => (
                <Row key={issue.issue_id}>
                  <Cell>{issue.issue_id}</Cell>
                  <Cell>{issue.issue_action}</Cell>
                  <Cell>
                    <Badge>{issue.issue_deadline}</Badge>
                  </Cell>
                  <Cell>{issue.issue_status}</Cell>
                  <Cell>{issue.reason}</Cell>
                  <Cell>
                    {issue.issue_status === 'TO_DO' ? (
                      <Button btnType='table' onClick={() => this.issueStart(issue.issue_id)}>
                        {t('Start')}
                      </Button>
                    ) : (
                      <Button btnType='table' onClick={() => this.onIssuePreview(issue.issue_id)}>
                        {t('Preview')}
                      </Button>
                    )}
                  </Cell>
                </Row>
              ))}
            </Table>
          )}
        </div>
      </ContentWrapper>
    )
  }
}

export default compose(withTranslation(), withApi, withRouter, withConfirmation)(IssuesList)
