import React from 'react'

import AnimatedRoute from 'components/AnimatedRoute'
import AnimatedSwitch from 'components/AnimatedSwitch'
import IssuesList from './Issues/IssuesList'
import IssueDetails from './Issues/IssueDetails'
import IssueConfirmation from './Issues/IssueConfirmation'
import { Redirect } from 'react-router-dom'

const Issues = () => {
  return (
    <AnimatedSwitch>
      <AnimatedRoute path='/issues/confirmation'>
        <IssueConfirmation />
      </AnimatedRoute>
      <AnimatedRoute path='/issues/details'>
        <IssueDetails />
      </AnimatedRoute>
      <AnimatedRoute path='/issues'>
        <IssuesList />
      </AnimatedRoute>
      <AnimatedRoute path='/issues*'>
        <Redirect to='/issues' />
      </AnimatedRoute>
    </AnimatedSwitch>
  )
}

export default Issues
