import React from 'react'
import { useTranslation } from 'react-i18next'
import { useHistory } from 'react-router-dom'

import ContentWrapper from 'components/ContentWrapper'
import Button from '../../components/Button'

const SupplyHelp = () => {
  const { t } = useTranslation()
  const history = useHistory()

  const onClick = () => {
    history.push('/supply')
  }

  return (
    <ContentWrapper className='rcs-supply-help'>
      <div className='rcs-supply-help__info'>
        <h3>{t('Help')}</h3>
        <h4>{t('Tap Continue to supply the rest of the products.')}</h4>
        <h4>{t('Tap Stop Resupply if you have no more products left.')}</h4>
      </div>
      <Button className='rcs-supply-help__continue' btnType='accent' onClick={onClick}>
        {t('Continue')}
      </Button>
    </ContentWrapper>
  )
}

export default SupplyHelp
