import React from 'react'
import { useTranslation } from 'react-i18next'

import { toteHeights, totePartitionings, toteStatuses } from 'config'

import Button from 'components/Button'
import Checkbox from 'components/Checkbox'
import Label from 'components/Label'
import Input from 'components/Input'
import Select from 'components/Select'
import PropTypes from 'prop-types'

const Filters = props => {
  const { values, onChange, onApply, onPresetSave, onPresetSelect, presets } = props
  const { t } = useTranslation()

  function filtersHandler (id, value) {
    const nextFilters = { ...values, [id]: value }

    if (onChange) {
      onChange(nextFilters)
    }
  }

  function handleInputs (e) {
    const { name, value } = e.target
    filtersHandler(name, value)
  }

  return (
    <div>
      <div className='rcs-totes__filters-header'>
        <div className='rcs-totes__filters-title'>{t('Filters')}</div>
        <div className='rcs-totes__filters-presets'>
          <div className='rcs-totes__filters-presets-title'>{t('Select pre-saved preset')}</div>
          <Select
            placeholder={t('Filter presets')}
            data={presets}
            itemToString={x => x.filter_id}
            itemToValue={x => x}
            onSelect={onPresetSelect}
          />
        </div>
      </div>

      <div className='rcs-totes__filters-fields'>
        <Label label={t('SKU')}>
          <Input placeholder={t('SKU')} onChange={handleInputs} name='sku' value={values.sku} />
        </Label>

        <Label label={t('Height')}>
          <Select
            data={toteHeights}
            placeholder={t('Height')}
            onSelect={x => filtersHandler('height', x)}
            value={values.height}
          />
        </Label>

        <Label label={t('Partitioning')}>
          <Select
            data={totePartitionings}
            placeholder={t('Partitioning')}
            onSelect={x => filtersHandler('partitioning', x)}
            value={values.partitioning}
          />
        </Label>

        <Label label={t('Status')}>
          <Select
            data={toteStatuses}
            placeholder={t('Status')}
            onSelect={x => filtersHandler('status', x)}
            value={values.status}
          />
        </Label>

        <Label label={t('Tote ID')}>
          <Input
            placeholder={t('Tote ID')}
            onChange={handleInputs}
            name='tote_id'
            value={values.tote_id}
          />
        </Label>

        <Label label={t('Order ID')}>
          <Input
            placeholder={t('Order ID')}
            onChange={handleInputs}
            name='order_id'
            value={values.order_id}
          />
        </Label>

        <div className='rcs-totes__filters-fields-buttons'>
          <Checkbox
            onChange={e => filtersHandler('only_in_progress', e.target.checked)}
            checked={values.only_in_progress}
          >
            {t('Only In Progress')}
          </Checkbox>
          <Button onClick={onApply}>{t('Apply')}</Button>
          <Button onClick={onPresetSave}>{t('Save Preset')}</Button>
        </div>
      </div>
    </div>
  )
}

Filters.propTypes = {
  values: PropTypes.object,
  onChange: PropTypes.func,
  onApply: PropTypes.func,
  onPresetSave: PropTypes.func,
  onPresetSelect: PropTypes.func,
  presets: PropTypes.array,
}

export default Filters
