import React from 'react'
import PropTypes from 'prop-types'
import cs from 'classnames'
import { useLocation } from 'react-router'
import { useTranslation } from 'react-i18next'

import Breadcrumbs from 'components/Breadcrumbs'
import Navigation from 'components/Navigation'
import User from 'components/User'
import SupplyNavActions from './Supply/SupplyNavActions'

import { useUser } from 'providers'
import { deliveryStoragePrefix } from 'config'

const regEx = /\/([A-Za-z-]+)\/?([A-Za-z-]+)?/

function getCrumb (isSupplyPath, match) {
  const delivery = JSON.parse(localStorage.getItem(deliveryStoragePrefix))
  if (isSupplyPath && delivery) {
    return delivery.displayName
  }

  if (match && match[2]) {
    return match[2]
  }

  return ''
}

const Navbar = props => {
  const { isSubnav, paths } = props
  const { user } = useUser()
  const { t } = useTranslation()
  const location = useLocation()

  const wrapperCls = cs('rcs-navbar', { 'rcs-navbar--navigation-only': isSubnav })
  const availablePaths = paths.filter(path => user.checkPermissions(path.roles))

  // Breadcrumbs
  const match = location.pathname.match(regEx)
  const isSupplyPath = match && match[1] === 'supply'

  const breadcrumbs = {
    parentPath: {
      path: match && `/${match[1]}`,
      label: match && t(match[1]),
    },
    crumb: getCrumb(isSupplyPath, match),
  }

  return (
    <div className={wrapperCls}>
      {!isSubnav && <Breadcrumbs {...breadcrumbs} />}
      {isSupplyPath ? <SupplyNavActions /> : <Navigation paths={availablePaths} />}
      {!isSubnav && <User />}
    </div>
  )
}

Navbar.propTypes = {
  isSubnav: PropTypes.bool,
  paths: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string,
      path: PropTypes.string,
    }),
  ),
}

export default Navbar
