import React, { useContext } from 'react'

import { parseJwt } from 'helpers'
import { userStoragePrefix } from 'config'

const UserContext = React.createContext()
const storedUser = JSON.parse(localStorage.getItem(userStoragePrefix)) || {}
const initialUser = {
  name: null,
  sub: null,
  roles: [],
}

export class UserProvider extends React.Component {
  state = {
    ...initialUser,
    ...storedUser,
  }

  setUser = (token, callback) => {
    const config = parseJwt(token)
    localStorage.setItem(userStoragePrefix, JSON.stringify(config))
    this.setState(
      {
        ...config,
        roles: Array.isArray(config.roles) ? config.roles : [],
      },
      callback,
    )
  }

  clearUser = callback => {
    localStorage.removeItem(userStoragePrefix)
    this.setState({ ...initialUser })
  }

  checkPermissions = requiredPerms => {
    const { roles } = this.state
    if (!Array.isArray(requiredPerms) || !Array.isArray(roles)) {
      return false
    }
    return requiredPerms.some(perm => roles.includes(perm))
  }

  render () {
    const { children } = this.props
    const user = {
      checkPermissions: this.checkPermissions,
      setUser: this.setUser,
      clearUser: this.clearUser,
      displayName: this.state.name,
      roles: this.state.roles,
    }
    return <UserContext.Provider value={{ user }}>{children}</UserContext.Provider>
  }
}

// Higher order component for class components mainly
export const withUser = Component => props => (
  <UserContext.Consumer>
    {user => <Component user={{ ...user.user }} {...props} />}
  </UserContext.Consumer>
)

// Hook to be used in functional component
export const useUser = () => {
  const { user } = useContext(UserContext)
  return { user }
}
