import React, { useContext } from 'react'
import UserApi from '../api/UserApi'
import { apiConfig } from 'config'

const Api = new UserApi(apiConfig)

const ApiContext = React.createContext(Api)

export const ApiProvider = ({ children }) => {
  return <ApiContext.Provider value={Api}>{children}</ApiContext.Provider>
}

// Higher order component for class components mainly
export const withApi = Component => props => (
  <ApiContext.Consumer>{api => <Component api={api} {...props} />}</ApiContext.Consumer>
)

// Hook to be used in functional component
export const useApi = () => {
  const api = useContext(ApiContext)
  return { api }
}
