import React from 'react'
import { useHistory } from 'react-router-dom'
import { useTranslation } from 'react-i18next'
import ContentWrapper from 'components/ContentWrapper'

import Button from 'components/Button'
import { ReactComponent as Confirmation } from 'assets/icons/confirmation.svg'
import { ReactComponent as Issue } from 'assets/icons/issue.svg'

const IssueConfirmation = () => {
  const { t } = useTranslation()
  const history = useHistory()

  return (
    <ContentWrapper className='rcs-service-issue-confirmation'>
      <div className='rcs-service-issue-confirmation__icons'>
        <Issue />
        <Confirmation className='rcs-service-issue-confirmation__icon-confirm' />
      </div>

      <h2 className='rcs-service-issue-confirmation__label'>{t('Well done! Issue finished')}</h2>

      <Button onClick={() => history.push('/issues')} btnType='neutral'>
        {t('To issue list')}
      </Button>
    </ContentWrapper>
  )
}

export default IssueConfirmation
