import React from 'react'
import PropTypes from 'prop-types'
import cs from 'classnames'

function getIsChecked (arr, id) {
  return arr.filter(x => x.item_id === id).length > 0
}

const ItemSelect = props => {
  const { items = [], maxItems = 3, onChange, checkedItems = [] } = props

  const change = (e, item) => {
    const { checked } = e.target
    const nextChecked = checkedItems.filter(x => x.item_id !== item.item_id)

    if (checked && checkedItems.length < maxItems) {
      nextChecked.push(item)
    }

    if (onChange) {
      onChange(nextChecked)
    }
  }

  const getItemCls = item => {
    return cs('rcs-item-select__item', {
      'rcs-item-select__item--checked':
        getIsChecked(checkedItems, item.item_id) ||
        item.inducted_quantity >= item.expected_quantity,
    })
  }

  return (
    <div className='rcs-item-select'>
      {items.map(item => (
        <label key={item.item_id} className={getItemCls(item)} htmlFor={item.sku_name}>
          <div>{item.sku_name || item.sku_id}</div>
          <div>
            {item.inducted_quantity} of {item.expected_quantity}
          </div>
          <input
            type='checkbox'
            name={item.item_id}
            id={item.sku_id}
            onChange={e => change(e, item)}
            checked={getIsChecked(checkedItems, item.item_id)}
          />
        </label>
      ))}
    </div>
  )
}

ItemSelect.propTypes = {
  checkedItems: PropTypes.array,
  items: PropTypes.array,
  onChange: PropTypes.func,
  maxItems: PropTypes.number,
}

export default ItemSelect
