import React from 'react'
import PropTypes from 'prop-types'
import cs from 'classnames'

const Button = props => {
  const { btnType = 'primary', className, ...restProps } = props
  const buttonCls = cs(className, 'rcs-button', `rcs-button__${btnType}`)

  return <button {...restProps} className={buttonCls} />
}

Button.propTypes = {
  btnType: PropTypes.oneOf(['primary', 'secondary', 'accent', 'neutral', 'table', 'ghost']),
}

export default Button
