import React from 'react'
import PropTypes from 'prop-types'
import cs from 'classnames'
import {useTranslation} from 'react-i18next'

import Badge from 'components/Badge'
import Button from 'components/Button'
import Input from 'components/Input'
import NumberInput from 'components/NumberInput'
import Label from 'components/Label'

const SlotResupply = props => {
  const {
    disabled,
    image_url,
    isConfirmed,
    name,
    sku_id,
    onChange,
    slotName,
    onBarcodeFocusChange,
    quantity,
    max,
  } = props

  const { t } = useTranslation()
  const wrapperCls = cs('rcs-tote-slot-resupply', { 'rcs-tote-slot-resupply--disabled': disabled })

  return (
    <div className={wrapperCls}>
      <div
        style={{ backgroundImage: `url(${image_url})` }}
        className='rcs-tote-slot-resupply__image'
      >
        {(name || sku_id) && (
          <Badge type='neutral' className='rcs-tote-slot-resupply__image-title'>
            {name || sku_id}
          </Badge>
        )}
      </div>
      <Label label={t('Product barcode')} className='rcs-tote-slot-resupply__barcode'>
        <Input
          value={sku_id}
          onChange={e => onChange('sku_id', e.target.value)}
          onFocus={() => onBarcodeFocusChange(slotName)}
          onBlur={() => onBarcodeFocusChange(null)}
        />
      </Label>
      <Label label={t('Quantity')} className='rcs-tote-slot-resupply__barcode'>
        <NumberInput
          value={quantity}
          onChange={value => onChange('quantity', value)}
          max={max}
        />
      </Label>
      <Button
        onClick={() => onChange('isConfirmed', !isConfirmed)}
        btnType={isConfirmed ? 'primary' : 'ghost'}
        className='rcs-tote-slot-resupply__confirm'
      >
        {t('Confirm')}
      </Button>
    </div>
  )
}

SlotResupply.propTypes = {
  disabled: PropTypes.bool,
  image_url: PropTypes.string,
  isConfirmed: PropTypes.bool,
  name: PropTypes.string,
  sku_id: PropTypes.string,
  quantity: PropTypes.number,
  onChange: PropTypes.func,
  slotName: PropTypes.string,
  onBarcodeFocusChange: PropTypes.func,
}

export default SlotResupply
