import * as yup from 'yup'

export const SupplyList = yup.object().shape({
  supply: yup.array().of(
    yup.object().shape({
      delivery_id: yup.string(),
      delivery_type: yup.string().nullable(),
      delivery_turn: yup.string().nullable(),
    }),
  ).nullable(),
})

export const SupplyItems = yup.object().shape({
  items: yup.array().of(
    yup.object().shape({
      item_id: yup.string(),
      sku_id: yup.string().nullable(),
      sku_name: yup.string().nullable(),
      category_id: yup.string().nullable(),
      expected_quantity: yup.number(),
      inducted_quantity: yup.number(),
    }).nullable(),
  ),
})

export const Sku = yup.object().shape({
  sku_id: yup.string(),
  image_url: yup.string().nullable(),
  name: yup.string().nullable(),
  category: yup.string().nullable(),
})
