import React from 'react'
import PropTypes from 'prop-types'

import { ReactComponent as Icon } from 'assets/icons/error.svg'

const Error = props => {
  const { children = 'Error occured' } = props

  return (
    <div className='rcs-error'>
      <Icon className='rcs-error__icon' />
      {children && <div className='rcs-error__message'>{children}</div>}
    </div>
  )
}

Error.propTypes = {
  children: PropTypes.node,
}

export default Error
