import React from 'react'
import PropTypes from 'prop-types'
import cs from 'classnames'

const Filter = props => {
  const { filters = [], label = 'Choose 3 products', onChange, value = 'all' } = props

  const select = e => {
    const { value } = e.target
    if (onChange) {
      onChange(value)
    }
  }

  const getItemCls = id => {
    return cs('rcs-filter__item', {
      'rcs-filter__item--checked': value === id,
    })
  }

  return (
    <div className='rcs-filter'>
      <div className='rcs-filter__label'>{label}</div>
      <div className='rcs-filter__items'>
        <label htmlFor='all' className={getItemCls('all')}>
          <input
            type='radio'
            className='rcs-filter__item'
            name={label}
            id='all'
            value='all'
            onChange={select}
          />
          All
        </label>
        {filters.map(x => (
          <label htmlFor={x} className={getItemCls(x)} key={x}>
            <input
              type='radio'
              className='rcs-filter__item'
              name={label}
              id={x}
              value={x}
              onChange={select}
            />
            {x}
          </label>
        ))}
      </div>
    </div>
  )
}

Filter.propTypes = {
  filters: PropTypes.array,
  label: PropTypes.string,
  value: PropTypes.string,
  onChange: PropTypes.func,
}

export default Filter
