import React from 'react'
import { compose } from 'recompose'
import { withTranslation } from 'react-i18next'

import { withApi, withConfirmation } from 'providers'
import { createFilterName, parseError } from 'helpers'

import Button from 'components/Button'
import ContentWrapper from 'components/ContentWrapper'
import Filters from './Orders/Filters'
import Loader from 'components/Loader'
import OrdersList from './Orders/OrdersList'

const initialFilters = {
  delivery_tote: '',
  order_status: '',
  sku: '',
  storage_tote: '',
  only_in_progress: false,
}

class Orders extends React.Component {
  state = {
    orders: [],
    selectedItems: [],
    requestState: 'LOADING',
    filters: initialFilters,
    presets: [],
  }

  componentDidMount () {
    this._isMounted = true
    this.ordersFetch()
    this.presetsFetch()
  }

  componentWillUnmount () {
    this._isMounted = false
  }

  beginRequest = request => {
    if (!this._isMounted) return
    this.setState({ requestState: 'LOADING' }, request)
  }

  failRequest = request => {
    if (!this._isMounted) return
    this.setState({ requestState: 'ERROR' }, request)
  }

  ordersFetch = () => {
    const { filters } = this.state
    const { api, t, confirmation } = this.props

    api
      .orderList(filters)
      .then(res => {
        if (!this._isMounted) return
        this.setState({ orders: res.body.orders, requestState: 'READY', selectedItems: [] })
      })
      .catch(err => {
        if (!this._isMounted) return
        const error = parseError(err)

        this.failRequest(
          confirmation.openConfirmation({
            children: error.message,
            header: error.header,
            onConfirm: () => this.beginRequest(this.ordersFetch),
            confirmLabel: t('Retry'),
            type: 'error',
          }),
        )
      })
  }

  presetsFetch () {
    const { api } = this.props

    api
      .filtersList({ target: 'ORDER' })
      .then(res => {
        if (!this._isMounted) return
        this.setState({ presets: res.body.filters })
      })
      .catch(() => {})
  }

  onFiltersChange = filters => {
    this.setState({ filters })
  }

  onPresetSelect = preset => {
    this.setState({ filters: { ...initialFilters, ...preset.fields } })
  }

  onPresetSave = () => {
    const { api } = this.props
    const { filters } = this.state
    const filterName = createFilterName(filters)

    const body = {
      fields: filters,
      target: 'ORDER',
      filter_id: filterName,
    }

    api.filtersCreate(body).then(() => this.presetsFetch())
  }

  onItemSelect = (id, selected) => {
    const { selectedItems } = this.state
    const nextSelectedItems = selectedItems.filter(x => x !== id)

    if (selected) {
      nextSelectedItems.push(id)
    }

    this.setState({ selectedItems: nextSelectedItems })
  }

  onItemSelectAll = e => {
    const { checked } = e.target

    if (checked) {
      const { orders } = this.state
      const nextSelectedItems = orders.map(x => x.order_id)
      this.setState({ selectedItems: nextSelectedItems })
    } else {
      this.setState({ selectedItems: [] })
    }
  }

  ordersDelete = ids => {
    const { api } = this.props
    if (Array.isArray(ids) && ids.length > 0) {
      api.orderDelete({ order_id: ids }).then(() => this.ordersFetch())
    }
  }

  ordersResend = ids => {
    const { api } = this.props
    if (Array.isArray(ids) && ids.length > 0) {
      api.orderResend({ order_id: ids }).then(() => this.ordersFetch())
    }
  }

  render () {
    const { t } = this.props
    const { orders, requestState, filters, selectedItems, presets } = this.state

    return (
      <ContentWrapper className='rcs-orders'>
        <div className='rcs-orders__filters'>
          <Filters
            onChange={this.onFiltersChange}
            onApply={this.ordersFetch}
            onPresetSave={this.onPresetSave}
            onPresetSelect={this.onPresetSelect}
            values={filters}
            presets={presets}
          />
        </div>
        {requestState === 'LOADING' ? (
          <Loader />
        ) : (
          <>
            <div className='rcs-orders__actions'>
              <Button btnType='secondary' onClick={() => this.ordersDelete(selectedItems)}>
                {t('Delete')}
              </Button>
              <Button onClick={() => this.ordersResend(selectedItems)}>{t('Resend')}</Button>
            </div>

            <div className='rcs-orders__table'>
              <OrdersList
                orders={orders}
                onItemSelect={this.onItemSelect}
                onItemSelectAll={this.onItemSelectAll}
                onItemDelete={this.ordersDelete}
                onItemResend={this.ordersResend}
                selectedItems={selectedItems}
              />
            </div>
          </>
        )}
      </ContentWrapper>
    )
  }
}

export default compose(withTranslation(), withApi, withConfirmation)(Orders)
