import React from 'react'
import PropTypes from 'prop-types'

const ProgressBar = props => {
  const { timeLeft, timeEstimation, label } = props
  const percent = parseInt(((timeEstimation - timeLeft) * 100) / timeEstimation, 10)

  return (
    <div className='rcs-progress-bar'>
      {label && <span>{label}</span>}
      <div className='rcs-progress-bar__wrapper'>
        <div className='rcs-progress-bar__loader' style={{ maxWidth: `${percent}%` }} />
      </div>
    </div>
  )
}

ProgressBar.propTypes = {
  label: PropTypes.string,
  timeLeft: PropTypes.number,
  timeEstimation: PropTypes.number,
}

export default ProgressBar
