import * as yup from 'yup'

export const Order = yup.object().shape({
  order_id: yup.string(),
  order_type: yup.string().nullable(),
  order_status: yup.string().nullable(),
  delivery_tote: yup.number().nullable(),
  created_at: yup.string().nullable(),
  collected_at: yup.string().nullable(),
  delivered_at: yup.string().nullable(),
  order_lines: [
    {
      sku_id: yup.string().nullable(),
      quantity: yup.number().nullable(),
      picked: yup.number().nullable(),
      failed: yup.number().nullable(),
      storage_totes: yup.array().nullable(),
    },
  ],
})

export const OrderList = yup.object().shape({
  orders: yup.array().of(Order).nullable(),
})
