import React, { useEffect, useState } from 'react'
import PropTypes from 'prop-types'

import Input from './Input'
import { ReactComponent as Plus } from 'assets/icons/plus.svg'
import { ReactComponent as Minus } from 'assets/icons/minus.svg'

const NumberInput = props => {
  const { value: propsValue, onChange, min = 0, max, ...restProps } = props
  const [value, setValue] = useState(0)

  const change = newValue => {
    if ((!isNaN(min) && newValue < min) || (!isNaN(max) && newValue > max)) {
      return
    }
    setValue(newValue)

    if (onChange) {
      onChange(newValue)
    }
  }

  useEffect(() => {
    if (propsValue != null) {
      setValue(propsValue)
    }
  }, [propsValue])

  return (
    <div className='rcs-number-input'>
      <button className='rcs-number-input__minus' onClick={() => change(value - 1)}>
        <Minus />
      </button>
      <Input
        value={value}
        className='rcs-number-input__element'
        type='number'
        min={min}
        onChange={e => change(e.target.value)}
        {...restProps}
      />
      <button className='rcs-number-input__plus' onClick={() => change(value + 1)}>
        <Plus />
      </button>
    </div>
  )
}

NumberInput.propTypes = {
  onChange: PropTypes.func,
  value: PropTypes.number,
}

export default NumberInput
