import React from 'react'
import PropTypes from 'prop-types'
import { useTranslation } from 'react-i18next'
import { Link } from 'react-router-dom'

import { ReactComponent as Home } from 'assets/icons/home.svg'

const Breadcrumbs = props => {
  const { parentPath, crumb } = props
  const { t } = useTranslation()

  return (
    <div className='rcs-breadcrumbs'>
      <Link className='rcs-breadcrumbs__parent' to='/'>
        <Home />
      </Link>
      {parentPath && (
        <>
          <div className='rcs-breadcrumbs__separator'>/</div>
          <Link className='rcs-breadcrumbs__parent' to={parentPath.path}>
            {t(parentPath.label)}
          </Link>
        </>
      )}
      {crumb && (
        <React.Fragment>
          <span className='rcs-breadcrumbs__separator'>/</span>
          <span className='rcs-breadcrumbs__crumb'>{crumb}</span>
        </React.Fragment>
      )}
    </div>
  )
}

Breadcrumbs.propTypes = {
  parentPath: PropTypes.shape({
    label: PropTypes.string,
    path: PropTypes.string,
  }),
  crumb: PropTypes.string,
}

export default Breadcrumbs
