import React, { useState, useEffect } from 'react'
import PropTypes from 'prop-types'

import { ReactComponent as Check } from 'assets/icons/check.svg'

const Checkbox = props => {
  const { children, defualtChecked, onChange, checked, ...restProps } = props
  const [isChecked, setIsChecked] = useState(checked)

  const change = e => {
    if (onChange) {
      onChange(e)
    }
    // Set checked only if checked prop is not passed
    if (checked == null) {
      setIsChecked(e.target.checked)
    }
  }

  useEffect(() => {
    if (checked != null) {
      setIsChecked(checked)
    }
  }, [checked])

  return (
    <label className='rcs-checkbox'>
      <span className='rcs-checkbox__label'>{children}</span>
      <input
        className='rcs-checkbox__input-element'
        type='checkbox'
        checked={isChecked}
        onChange={change}
        {...restProps}
      />
      <span className='rcs-checkbox__tick'>{isChecked && <Check />}</span>
    </label>
  )
}

Checkbox.propTypes = {
  defaultChecked: PropTypes.bool,
  checked: PropTypes.bool,
  onChange: PropTypes.func,
}

export default Checkbox
