import React from 'react'
import { Switch, Route, Router } from 'react-router-dom'
import { createBrowserHistory } from 'history'

import Navbar from 'pages/Navbar'
import Login from 'pages/Login'
import ProtectedRoute from 'components/ProtectedRoute'
import UserRedirectRoute from 'components/UserRedirectRoute'
import { paths, routerBasepath } from 'config'

const AppRouter = () => {
  const history = createBrowserHistory({ basename: routerBasepath })

  return (
    <Router basepath={routerBasepath} history={history}>
      <Switch>
        <Route exact path='/login'>
          <Login />
        </Route>
        {paths.map(path => (
          <ProtectedRoute key={path.path} path={path.path} roles={path.roles}>
            <Navbar paths={paths} />
            <path.component />
          </ProtectedRoute>
        ))}
        <UserRedirectRoute path='*' />
      </Switch>
    </Router>
  )
}

export default AppRouter
