import React from 'react'
import PropTypes from 'prop-types'
import cs from 'classnames'

const Badge = props => {
  const { className, type = 'success', ...restProps } = props
  const wrapperCls = cs(className, 'rcs-badge', `rcs-badge--${type}`)

  return <span className={wrapperCls} {...restProps} />
}

Badge.propTypes = {
  type: PropTypes.oneOf(['success', 'warn', 'info', 'neutral']),
}

export default Badge
