import React from 'react'

import AnimatedRoute from 'components/AnimatedRoute'
import AnimatedSwitch from 'components/AnimatedSwitch'
import InductLoading from "pages/Shared/ToteLoading";
import Resupply from "./Shared/SupplyTote";
import LoadTote from "../components/LoadTote";
import InductConfirmation from "./Induct/InductConfirmation";

const Induct = () => {
    return (
        <AnimatedSwitch>
            <AnimatedRoute path='/induct/confirmation'>
                <InductConfirmation/>
            </AnimatedRoute>
            <AnimatedRoute path='/induct/tote-supply'>
                <Resupply failureUrl='/induct' successUrl='/induct/confirmation'/>
            </AnimatedRoute>
            <AnimatedRoute path='/induct'>
                <InductLoading mode='induct'>
                    <LoadTote/>
                </InductLoading>
            </AnimatedRoute>
        </AnimatedSwitch>
    )
}

export default Induct
