import React from 'react'
import PropTypes from 'prop-types'
import cs from 'classnames'

import Button from 'components/Button'

const Confirmation = props => {
  const {
    header,
    children,
    onCancel,
    onConfirm,
    type = 'confirm',
    confirmLabel = 'Confirm',
    cancelLabel = 'Cancel',
  } = props
  const containerCls = cs('rcs-confirmation__container', {
    [`rcs-confirmation__container--${type}`]: type,
  })

  return (
    <div className='rcs-confirmation'>
      <div className={containerCls}>
        <h1 className='rcs-confirmation__header'>{header}</h1>

        <div className='rcs-confirmation__content'>{children}</div>

        <div className='rcs-confirmation__buttons'>
          <Button onClick={onCancel} btnType='neutral'>
            {cancelLabel}
          </Button>
          <Button onClick={onConfirm} btnType='accent'>
            {confirmLabel}
          </Button>
        </div>
      </div>
    </div>
  )
}

Confirmation.propTypes = {
  header: PropTypes.node,
  children: PropTypes.node,
  onConfirm: PropTypes.func,
  onCancel: PropTypes.func,
  confirmLabel: PropTypes.string,
  cancelLabel: PropTypes.string,
  type: PropTypes.oneOf(['confirm', 'error']),
}

export default Confirmation
