import React from 'react'
import { withRouter } from 'react-router'
import { compose } from 'recompose'
import { withTranslation } from 'react-i18next'

import { withSupplyInfo, withApi, withConfirmation } from 'providers'

import Button from 'components/Button'
import ContentWrapper from 'components/ContentWrapper'
import ItemSelect from 'components/ItemSelect'
import Loader from 'components/Loader'
import { parseError } from '../../helpers'

class ProgressPreview extends React.Component {
  state = {
    requestState: 'NONE',
    products: [],
  }

  componentDidMount () {
    this._isMounted = true
    this.productsFetch()
  }

  componentWillUnmount () {
    this._isMounted = false
  }

  beginRequest = request => {
    if (!this._isMounted) return
    this.setState({ requestState: 'LOADING' }, request)
  }

  failRequest = request => {
    if (!this._isMounted) return
    this.setState({ requestState: 'ERROR' }, request)
  }

  productsFetch = () => {
    const { api, supplyInfo, confirmation, t, history } = this.props
    const supply = supplyInfo.getDelivery()

    if (!supply) {
      return history.push('/supply')
    }

    this.beginRequest(() =>
      api
        .supplyItemsList({ supply_id: supply.supply_id })
        .then(res => {
          if (!this._isMounted) return
          const products = res.body.items
          supplyInfo.setDelivery(supply)

          this.setState({
            products: products,
            requestState: 'READY',
            currentStep: ['products_selection'],
            inductedQuantity: products.reduce((prev, curr) => prev + curr.inducted_quantity, 0),
            expectedQuantity: products.reduce((prev, curr) => prev + curr.expected_quantity, 0),
          })
        })
        .catch(err => {
          if (!this._isMounted) return
          const error = parseError(err)

          confirmation.openConfirmation({
            children: error.message,
            header: error.header,
            onConfirm: () => this.beginRequest(this.onClose),
            confirmLabel: t('Retry'),
            type: 'error',
          })
        }),
    )
  }

  onClick = () => {
    const { history, supplyInfo } = this.props
    const { inductedQuantity, expectedQuantity } = this.state
    if (inductedQuantity >= expectedQuantity) {
      supplyInfo.resetDelivery()
    }
    history.push('/supply')
  }

  render () {
    const { t } = this.props
    const { products, requestState, inductedQuantity, expectedQuantity } = this.state
    const isDeliveryCompleted = inductedQuantity >= expectedQuantity

    if (requestState === 'LOADING') {
      return <Loader />
    }

    return (
      <ContentWrapper className='rcs-supply-progress-preview'>
        <div className='rcs-supply-progress-preview__items'>
          <div className='rcs-supply-progress-preview__items-header'>
            <div>Inducted</div>
            <div>
              {inductedQuantity} of {expectedQuantity}
            </div>
          </div>
          <ItemSelect items={products} />
        </div>
        <div className='rcs-supply-progress-preview__info'>
          <h3>
            {t('You have put {{inductedQuantity}} out of {{expectedQuantity}} SKU', {
              inductedQuantity,
              expectedQuantity,
            })}
          </h3>
          <h4>{t('Tap Continue to supply the rest of the products.')}</h4>
          <h4>{t('Tap Stop Resupply if you have no more products left.')}</h4>
          <h4>{t('Tap Help if you need more information.')}</h4>
        </div>
        <Button
          className='rcs-supply-progress-preview__continue'
          btnType='accent'
          onClick={this.onClick}
        >
          {isDeliveryCompleted ? t('Finish supply') : t('Continue')}
        </Button>
      </ContentWrapper>
    )
  }
}

export default compose(
  withTranslation(),
  withRouter,
  withApi,
  withConfirmation,
  withSupplyInfo,
)(ProgressPreview)
