import React from 'react'
import { useHistory } from 'react-router-dom'

import { useUser } from 'providers'
import Dropdown from 'components/Dropdown'

const testImage = 'https://robohash.org/Robert Redford'

const User = props => {
  const { user } = useUser()
  const history = useHistory()

  const dropdownContent = (
    <div className='rcs-user__dropdown' onClick={() => user.clearUser(() => history.push('/'))}>
      Log out
    </div>
  )

  return (
    <div className='rcs-user'>
      <Dropdown content={dropdownContent}>
        <div className='rcs-user__data'>
          <img className='rcs-user__image' src={testImage} alt='user' />
          <div className='rcs-user__name'>{user.displayName}</div>
        </div>
      </Dropdown>
    </div>
  )
}

export default User
