import React from 'react'

import AnimatedRoute from 'components/AnimatedRoute'
import AnimatedSwitch from 'components/AnimatedSwitch'
import Delivery from './Supply/Delivery'
import SupplyHelp from './Supply/SupplyHelp'
import ProgressPreview from './Supply/ProgressPreview'
import Resupply from './Shared/SupplyTote'
import SupplyConfirmation from './Supply/SupplyConfirmation'
import SupplyLoading from './Shared/ToteLoading'
import { Redirect } from 'react-router-dom'
import WaitForTote from "./Supply/WaitForTote";

const Development = () => {
  return (
    <AnimatedSwitch>
      <AnimatedRoute path='/supply/loading'>
        <SupplyLoading mode='supply'>
            <WaitForTote />
        </SupplyLoading>
      </AnimatedRoute>
      <AnimatedRoute path='/supply/confirmation'>
        <SupplyConfirmation />
      </AnimatedRoute>
      <AnimatedRoute path='/supply/progress-preview'>
        <ProgressPreview />
      </AnimatedRoute>
      <AnimatedRoute path='/supply/tote-supply'>
        <Resupply failureUrl='/supply' successUrl="/supply/confirmation"/>
      </AnimatedRoute>
      <AnimatedRoute path='/supply/help'>
        <SupplyHelp />
      </AnimatedRoute>
      <AnimatedRoute path='/supply'>
        <Delivery />
      </AnimatedRoute>
      <AnimatedRoute path='/supply*'>
        <Redirect to='/supply' />
      </AnimatedRoute>
    </AnimatedSwitch>
  )
}

export default Development
