import React, { useState } from 'react'
import { useTranslation } from 'react-i18next'
import { useHistory } from 'react-router-dom'

import { useApi } from 'api'
import { useUser } from 'providers'

import MountTransition from 'components/MountTransition'
import Button from 'components/Button'
import Input from 'components/Input'
import Label from 'components/Label'

const Login = props => {
  const { t } = useTranslation()
  const { api } = useApi()
  const { user } = useUser()
  const history = useHistory()
  const [credentials, setCredentials] = useState({ username: '', password: '' })
  const [requestState, setRequestState] = useState('NONE')

  const changeHandler = e => {
    const { name, value } = e.target
    setCredentials({
      ...credentials,
      [name]: value,
    })
  }

  const isSubmitDisabled =
    !credentials.username || !credentials.password || requestState === 'PENDING'

  const submitHandler = e => {
    e.preventDefault()
    setRequestState('PENDING')

    if (!isSubmitDisabled) {
      api
        .userLogin(credentials)
        .then(res => user.setUser(res.body.token, () => history.push('development')))
        .catch(() => {
          setRequestState('ERROR')
        })
    }
  }

  return (
    <MountTransition className='rcs-login' slide={30}>
      <div className='rcs-login__lead'>
        <div className='rcs-login__lead-logo' />
        <div className='rcs-login__lead-slogan'>
          <h1>Welcome to RCS</h1>
          <h2>The very first, fully automated store</h2>
        </div>
      </div>
      <form onSubmit={submitHandler} className='rcs-login__user'>
        <div className='rcs-login__user-header'>
          {t('Provide your credentials and press login')}
        </div>
        <Label className='rcs-login__user-name' label={t('Login')}>
          <Input onChange={changeHandler} name='username' required />
        </Label>
        <Label className='rcs-login__user-password' label={t('Password')}>
          <Input onChange={changeHandler} type='password' name='password' required />
        </Label>
        <Button disabled={isSubmitDisabled} btnType='accent' type='submit'>
          {t('Login')}
        </Button>
      </form>
    </MountTransition>
  )
}

export default Login
