import React from 'react'
import PropTypes from 'prop-types'
import cs from 'classnames'

const Label = props => {
  const { className, children, label, ...restProps } = props
  const wrapperCls = cs(className, 'rcs-label')
  return (
    <div className={wrapperCls} {...restProps}>
      {label && <div className='rcs-label__label'>{label}</div>}
      {children}
    </div>
  )
}

Label.propTypes = {
  label: PropTypes.node,
}

export default Label
