import React, { useEffect, useRef, useState } from 'react'
import PropTypes from 'prop-types'
import cs from 'classnames'

import { ReactComponent as Arrow } from 'assets/icons/arrow.svg'

const Dropdown = props => {
  const { children, content } = props
  const [isOpen, setIsOpen] = useState(false)
  const ref = useRef()
  const dropdownCls = cs('rcs-dropdown__menu', { 'rcs-dropdown__menu--open': isOpen })
  const arrowCls = cs('rcs-dropdown__arrow', { 'rcs-dropdown__arrow--open': isOpen })

  const clickHandler = e => {
    if (!ref.current.contains(e.target) && isOpen) {
      setIsOpen(false)
    }
  }

  useEffect(() => {
    document.addEventListener('click', clickHandler)
    return () => document.removeEventListener('click', clickHandler)
  })

  return (
    <div className='rcs-dropdown'>
      {React.cloneElement(children, { onClick: () => setIsOpen(!isOpen) })}
      <div className={dropdownCls} ref={ref}>
        {content}
      </div>
      <Arrow className={arrowCls} />
    </div>
  )
}

Dropdown.propTypes = {
  children: PropTypes.node,
  content: PropTypes.node,
}

export default Dropdown
