import React from 'react'
import { Link } from 'react-router-dom'
import { useTranslation } from 'react-i18next'

import { useSupplyInfo, useUser } from 'providers'

const SupplyNavActions = props => {
  const { t } = useTranslation()
  const { supplyInfo } = useSupplyInfo()
  const { user } = useUser()

  const onStop = () => {
    user.clearUser()
    supplyInfo.resetDelivery()
  }

  return (
    <div className='rcs-supply-nav-actions'>
      <Link className='rcs-supply-nav-actions__link' to='/supply/help'>
        {t('Help')}
      </Link>
      <Link className='rcs-supply-nav-actions__link' to='/supply/progress-preview'>
        {t('Progress Preview')}
      </Link>
      <Link className='rcs-supply-nav-actions__stop' onClick={onStop} to='/'>
        {t('Stop Resupply')}
      </Link>
    </div>
  )
}

export default SupplyNavActions
