import * as yup from 'yup'

export const Issue = yup.object().shape({
  issue_id: yup.string().required(),
  issue_action: yup.string().nullable(),
  issue_deadline: yup.string().nullable(),
  issue_status: yup.string().nullable(),
  reason: yup.string().nullable(),
  tote_id: yup.string().nullable(),
  notes: yup.string().nullable(),
})

export const IssueList = yup.object().shape({
  issues: yup
    .array()
    .of(Issue)
    .nullable(),
})
