import React, { useContext } from 'react'
import { findDOMNode } from 'react-dom'

import Confirmation from 'components/Confirmation'
import Modal from 'components/Modal'
import MountTransition from 'components/MountTransition'

const ConfirmationContext = React.createContext()

const initialState = {
  isOpen: false,
  children: null,
  header: null,
  onCancel: null,
  onConfirm: null,
}

export class ConfirmationProvider extends React.PureComponent {
  state = initialState

  getModalRef = node => {
    this.modal = findDOMNode(node)
  }

  componentDidUpdate (prevProps, prevState, snapshot) {
    document.addEventListener('click', this.closeConfirmation)
  }

  componentWillUnmount (prevProps, prevState, snapshot) {
    document.removeEventListener('click', this.closeConfirmation)
  }

  closeConfirmation = e => {
    if (e.target !== this.modal && !this.modal.contains(e.target)) {
      this.setState(initialState)
    }
  }

  openConfirmation = confirmationProps => {
    this.setState({ isOpen: true, ...confirmationProps })
  }

  onCancel = () => {
    const { onCancel } = this.state

    if (onCancel) {
      onCancel()
    }
    this.setState(initialState)
  }

  onConfirm = () => {
    const { onConfirm } = this.state

    if (onConfirm) {
      onConfirm()
    }
    this.setState(initialState)
  }

  render () {
    const { isOpen, ...confirmationProps } = this.state
    const { children: propsChildren } = this.props

    return (
      <>
        <Modal isOpen={isOpen} ref={this.getModalRef}>
          {isOpen && (
            <MountTransition
              slide={-30}
              transition={{ duration: 0.05, type: 'spring', stiffness: 1000 }}
            >
              <Confirmation
                {...confirmationProps}
                onCancel={this.onCancel}
                onConfirm={this.onConfirm}
              />
            </MountTransition>
          )}
        </Modal>
        <ConfirmationContext.Provider value={{ openConfirmation: this.openConfirmation }}>
          {propsChildren}
        </ConfirmationContext.Provider>
      </>
    )
  }
}

// Higher order component for class components mainly
export const withConfirmation = Component => props => (
  <ConfirmationContext.Consumer>
    {confirmation => <Component confirmation={confirmation} {...props} />}
  </ConfirmationContext.Consumer>
)

// Hook to be used in functional component
export const useConfirmation = () => {
  const confirmation = useContext(ConfirmationContext)
  return { confirmation }
}

export default ConfirmationProvider
