export function parseError (error) {
  let parsedError

  if (!error || !(typeof error === 'object') || !error.body) {
    parsedError = {
      header: 'Unknown error',
      message: 'Unknown error occured.',
    }
  } else {
    parsedError = {
      header: error.body.error_code || error.body.error,
      message: error.body.error_message || error.body.message,
    }
  }

  return parsedError
}
