import React from 'react'
import PropTypes from 'prop-types'
import {compose} from 'recompose'
import {withTranslation} from 'react-i18next'
import SockJS from 'sockjs-client'
import Stomp from 'stompjs'

import {withApi, withSupplyInfo} from 'providers'
import {wssConfig} from 'config'

class ToteLoading extends React.Component {
    state = {
        currentProducts: this.props.supplyInfo.getCurrentProducts(),
    }

    componentDidMount() {
        const {mode, history, api} = this.props
        const {currentProducts} = this.state

        if (mode === 'supply' && (!Array.isArray(currentProducts) || currentProducts.length < 1)) {
            return history.push('/supply')
        } else if (mode === 'induct') {
            api.openGate()
                .catch(() => {
                    console.log("Gate was not open!")
                })
        }

        this.websocketReconnectTimeout = setTimeout(() => this.initWebsocket(), 1000)
    }

    componentWillUnmount() {
        this.closeWebsocket()
    }

    initWebsocket = () => {
        const {history, mode} = this.props
        this.sock = SockJS(wssConfig.baseUrl + wssConfig.operatorPanel)
        this.stompClient = Stomp.over(this.sock)

        this.stompClient.connect(
            {},
            frame => {
                this.stompSubscription = this.stompClient.subscribe(wssConfig.toteInGate, function (res) {
                    const tote = JSON.parse(res.body)
                    if (tote.tote_id) {
                        if (mode === 'supply') {
                            history.push(`/supply/tote-supply?id=${tote.tote_id}`)
                        } else {
                            history.push(`/induct/tote-supply?id=${tote.tote_id}`)
                        }
                    }
                })
            },
            this.errorWebsocketCallback,
        )

        this.sock.onclose = this.errorWebsocketCallback
    }

    errorWebsocketCallback = () => {
        this.closeWebsocket()
        this.websocketReconnectTimeout = setTimeout(() => this.initWebsocket(), 1000)
    }

    closeWebsocket() {
        if (this.sock) {
            this.sock.close()
        }

        if (this.websocketReconnectTimeout) {
            clearTimeout(this.websocketReconnectTimeout)
            this.websocketReconnectTimeout = null
        }

        if (this.stompSubscription) {
            this.stompSubscription.unsubscribe()
        }

        if (this.stompClient) {
            this.stompClient.disconnect()
        }
    }

    render() {
        return <>{this.props.children}</>
    }
}

ToteLoading.propTypes = {
    products: PropTypes.arrayOf(PropTypes.string),
    time: PropTypes.number,
    mode: PropTypes.string
}

export default compose(withTranslation(), withSupplyInfo, withApi)(ToteLoading)
