import React from 'react'
import { compose } from 'recompose'
import { withTranslation } from 'react-i18next'
import { withRouter } from 'react-router'

import { withApi, withConfirmation } from 'providers'

import Badge from '../../components/Badge'
import Button from '../../components/Button'
import ContentWrapper from 'components/ContentWrapper'
import Error from 'components/Error'
import Loader from 'components/Loader'
import { Cell, Head, Row, Table } from '../../components/Table'
import Textarea from 'components/Textarea'
import { parseError } from '../../helpers'

const headers = [
  { id: 'id', label: 'ID' },
  { id: 'action', label: 'Action' },
  { id: 'deadline', label: 'Deadline' },
  { id: 'status', label: 'Status' },
  { id: 'reason', label: 'Reason' },
]

class IssueDetails extends React.Component {
  state = {
    issue: {},
    requestState: 'LOADING',
  }

  componentDidMount () {
    this._isMounted = true
    this.beginRequest(this.fetchIssue)
  }

  componentWillUnmount () {
    this._isMounted = false
  }

  beginRequest = request => {
    if (!this._isMounted) return
    this.setState({ requestState: 'LOADING' }, request)
  }

  failRequest = request => {
    if (!this._isMounted) return
    this.setState({ requestState: 'ERROR' }, request)
  }

  pendingRequest = request => {
    if (!this._isMounted) return
    this.setState({ requestState: 'PENDING' }, request)
  }

  fetchIssue = () => {
    const { api, confirmation, t, location } = this.props
    const urlParams = new URLSearchParams(location.search)
    const id = urlParams.get('issue_id')

    api
      .issueDetails({ issue_id: id })
      .then(res => {
        if (!this._isMounted) return
        this.setState({ issue: res.body, requestState: 'READY' })
      })
      .catch(err => {
        if (!this._isMounted) return
        const error = parseError(err)

        this.failRequest(() =>
          confirmation.openConfirmation({
            children: error.message,
            header: error.header,
            onConfirm: () => this.beginRequest(this.fetchIssue),
            confirmLabel: t('Retry'),
            type: 'error',
          }),
        )
      })
  }

  onCancel = () => {
    const { history } = this.props
    history.push('/issues')
  }

  onClose = () => {
    const { api, confirmation, match, history, t } = this.props
    const { issue } = this.state
    const { id } = match.params

    this.pendingRequest(() =>
      api
        .issueClose({ issue_id: id, notes: issue.notes, issue_status: issue.issue_status })
        .then(() => {
          if (!this._isMounted) return
          history.push('/issues')
        })
        .catch(err => {
          if (!this._isMounted) return
          const error = parseError(err)

          confirmation.openConfirmation({
            children: error.message,
            header: error.header,
            onConfirm: () => this.beginRequest(this.onClose),
            confirmLabel: t('Retry'),
            type: 'error',
          })
        }),
    )
  }

  render () {
    const { t } = this.props
    const { issue, requestState } = this.state

    const isButtonDisabled = requestState === 'PENDING'

    if (requestState === 'LOADING') {
      return <Loader />
    }

    if (requestState === 'ERROR') {
      return <Error />
    }

    return (
      <ContentWrapper className='rcs-service-issue-details'>
        <h2>{t('Issues details')}</h2>

        <Table
          className='rcs-service-issue-details__table'
          head={<Head headers={headers} withActions={false} />}
        >
          <Row key={issue.issue_id}>
            <Cell>{issue.issue_id}</Cell>
            <Cell>{issue.issue_action}</Cell>
            <Cell>
              <Badge>{issue.issue_deadline}</Badge>
            </Cell>
            <Cell>{issue.issue_status}</Cell>
            <Cell>{issue.reason}</Cell>
          </Row>
        </Table>

        <div className='rcs-service-issue-details__issue-info'>
          {t('Tote on its way, Gate will be open when ready.')}
        </div>

        <Textarea
          onChange={e => this.setState({ issue: { ...issue, notes: e.target.value } })}
          placeholder={t('Please provide notes if necessary')}
        />

        <div className='rcs-service-issue-details__actions'>
          <Button disabled={isButtonDisabled} btnType='neutral'>
            {t('Cancel')}
          </Button>
          <Button disabled={isButtonDisabled} onClick={this.onClose} btnType='accent'>
            {t('Done')}
          </Button>
        </div>
      </ContentWrapper>
    )
  }
}

export default compose(withTranslation(), withRouter, withApi, withConfirmation)(IssueDetails)
