export function getDeliveryDisplayName (supply, t = x => x) {
  if (!supply) {
    return ''
  }

  const distribution = supply.distribution_type ? `${supply.distribution_type.toLowerCase()}, ` : ''
  const turn = supply.supply_turn ? `${t('Turn')} ${supply.supply_turn} ` : ''
  const id = supply.supply_id ? `(${supply.supply_id})` : ''

  return `${distribution}${turn}${id}`
}
