import React from 'react'
import { Route, Redirect } from 'react-router-dom'

import { userRedirections } from 'config'
import { useUser } from 'providers'

const UserRedirectRoute = props => {
  const { roles, ...restProps } = props
  const { user } = useUser()
  const userRoles = user.roles || []
  let redirectTo = '/login'

  for (let roleSettings of userRedirections) {
    if (userRoles.some(x => x === roleSettings.role)) {
      redirectTo = roleSettings.redirectTo
      break
    }
  }

  return (
    <Route {...restProps}>
      <Redirect to={redirectTo} />
    </Route>
  )
}

export default UserRedirectRoute
