function ValidationError (message, header) {
  this.body = { error_message: message, error_code: header }
  this.name = 'ValidationError'
}

export default class ApiClient {
  constructor (config) {
    this.baseUrl = (config ? config.baseUrl : '/').replace(/\/+$/, '')
    this.additionalCallOptions = config.additionalCallOptions || {}
    this.additionalCallHeaders = config.additionalCallHeaders || {}
  }

  /**
   * Build url by adding path and query to base URL
   * @param {String} path APi resource path
   * @param {Object} [query]  Query to be parsed to string
   * @returns {string}
   */
  buildUrl (path, query = {}) {
    if (!path.match(/^\//)) {
      path = '/' + path
    }
    const url = `${this.baseUrl}${path}`
    for (let key in query) {
      if (this.isEmpty(query[key])) {
        delete query[key]
      }
    }
    query = new URLSearchParams(query)

    if (query && query.toString().length > 0) {
      return `${url}?${query}`
    }

    return url
  }

  /**
   *
   * @param response
   * @returns {Promise<{ok: boolean, status: string, data: object}>}
   */
  parseResponse = async response => {
    const data = {
      ok: response.ok,
      status: response.status,
    }

    try {
      data.body = await response.json()
    } catch (e) {}

    if (!data.ok) {
      throw data
    }

    return data
  }

  /**
   * Check if value is null, undefined or empty string
   *
   * @param {*} value
   * @returns {boolean}
   */
  isEmpty (value) {
    // eslint-disable-next-line eqeqeq
    if (value != undefined && value !== '') {
      return false
    }
    return true
  }

  /**
   * Filter out empty fileds in queries
   *
   * @param {Object} query
   * @returns {Object}
   */
  filterEmptyQueryParms (query) {
    const newQuery = {}
    for (let key in query) {
      if (query.hasOwnProperty(key) && !this.isEmpty(query[key])) {
        newQuery[key] = query[key]
      }
    }
    return newQuery
  }

  /**
   * Validate data
   *
   * @param data
   * @param validator
   * @returns {Promise<any>}
   */
  async validateData (data, validator) {
    try {
      const validatedData = await validator.validate(data, {
        strict: true,
        stripUnknown: true,
      })
      return validatedData
    } catch (e) {
      console.error(e)
      throw new ValidationError(e.message, 'Model Validation Error')
    }
  }

  /**
   * HTTP GET handler
   *
   * @param {String} path
   * @param {Object} [query]
   * @param {ObjectSchema} [resModel]
   * @param {Object} [options]
   * @returns {Promise<{ok: Boolean, status: String, data: Object}>}
   */
  async get (path, query, resModel, options = {}) {
    const url = this.buildUrl(path, query)
    const response = await window.fetch(url, {
      method: 'GET',
      mode: 'cors',
      headers: {
        'Content-Type': 'application/json',
        ...this.additionalCallHeaders,
      },
      ...this.additionalCallOptions,
      ...options,
    })

    const parsed = await this.parseResponse(response)

    if (resModel) {
      parsed.body = await this.validateData(parsed.body, resModel)
    }

    return parsed
  }

  /**
   * HTTP POST handler
   *
   * @param {String} path
   * @param {Object} body
   * @param {ObjectSchema} [resModel]
   * @param {Object} [options]
   * @returns {Promise<{ok: Boolean, status: String, data: Object}>}
   */
  async post (path, body, resModel, options = {}) {
    const url = this.buildUrl(path)
    const response = await window.fetch(url, {
      method: 'POST',
      mode: 'cors',
      headers: {
        'Content-Type': 'application/json',
        ...this.additionalCallHeaders,
      },
      ...this.additionalCallOptions,
      ...options,
      body: JSON.stringify(body),
    })

    const parsed = await this.parseResponse(response)

    if (resModel) {
      parsed.body = await this.validateData(parsed.body, resModel)
    }

    return parsed
  }
}
