import React from 'react'
import { compose } from 'recompose'
import { withTranslation } from 'react-i18next'

import { withApi, withConfirmation } from 'providers'
import { parseError } from 'helpers'
import { toteHeights, toteOrientations, totePartitionings, toteStatuses } from 'config'

import { ReactComponent as CloseIcon } from 'assets/icons/close.svg'
import Button from 'components/Button'
import ContentWrapper from 'components/ContentWrapper'
import Input from 'components/Input'
import Label from 'components/Label'
import Select from 'components/Select'
import Slot from './ToteEdit/Slot'

class ToteEdit extends React.Component {
  state = {
    tote: {},
    requestState: 'LOADING',
  }

  componentDidMount () {
    this._isMounted = true
    this.detailsFetch()
  }

  componentWillUnmount () {
    this._isMounted = false
  }

  beginRequest = request => {
    if (!this._isMounted) return
    this.setState({ requestState: 'LOADING' }, request)
  }

  failRequest = request => {
    if (!this._isMounted) return
    this.setState({ requestState: 'ERROR' }, request)
  }

  finishRequest = request => {
    if (!this._isMounted) return
    this.setState({ requestState: 'READY' }, request)
  }

  pendingRequest = request => {
    if (!this._isMounted) return
    this.setState({ requestState: 'PENDING' }, request)
  }

  detailsFetch () {
    const { api, confirmation, t, match } = this.props
    const { id } = match.params

    api
      .toteDetails({ tote_id: id })
      .then(res => {
        if (!this._isMounted) return
        this.setState({ tote: res.body, requestState: 'READY' })
      })
      .catch(err => {
        const error = parseError(err)

        this.failRequest(() =>
          confirmation.openConfirmation({
            children: error.message,
            header: error.header,
            onConfirm: () => this.beginRequest(this.detailsFetch),
            confirmLabel: t('Retry'),
            type: 'error',
          }),
        )
      })
  }

  onClose = () => {
    const { history } = this.props
    history.push('/development/totes')
  }

  onDelete = () => {
    const { api, confirmation, t, match } = this.props
    const { id } = match.params

    this.pendingRequest(() =>
      api
        .toteDelete({ tote_id: id })
        .then(res => {
          if (!this._isMounted) return
          this.onClose()
        })
        .catch(err => {
          if (!this._isMounted) return
          const error = parseError(err)

          this.finishRequest(() =>
            confirmation.openConfirmation({
              children: error.message,
              header: error.header,
              onConfirm: () => this.pendingRequest(this.onDelete),
              confirmLabel: t('Retry'),
              type: 'error',
            }),
          )
        }),
    )
  }

  onUpdate = () => {
    const { api, confirmation, t } = this.props
    const { tote } = this.state

    this.pendingRequest(() =>
      api
        .toteUpdate({ ...tote })
        .then(res => {
          if (!this._isMounted) return
          this.onClose()
        })
        .catch(err => {
          if (!this._isMounted) return
          const error = parseError(err)

          this.finishRequest(() =>
            confirmation.openConfirmation({
              children: error.message,
              header: error.header,
              onConfirm: () => this.pendingRequest(this.onUpdate),
              confirmLabel: t('Retry'),
              type: 'error',
            }),
          )
        }),
    )
  }

  onFormChange = (name, value) => {
    const { tote } = this.state
    const nextForm = { ...tote, [name]: value }
    this.setState({ tote: nextForm })
  }

  getSlot = (field, fieldValue) => {
    const { tote } = this.state
    return tote.slots && tote.slots.find(x => x[field] === fieldValue)
  }

  onSlotChange = (slotField, name, value) => {
    const { tote } = this.state
    const nextSlots = tote.slots.filter(x => x.ordinal !== slotField)
    const prevSlotInfo = this.getSlot('ordinal', slotField)
    const nestSlotInfo = {
      ...prevSlotInfo,
      [name]: value,
    }
    nextSlots.push(nestSlotInfo)
    this.setState({
      tote: {
        ...tote,
        slots: nextSlots,
      },
    })
  }

  render () {
    const { t } = this.props
    const { tote, requestState } = this.state
    const slot0 = this.getSlot('ordinal', 0)
    const slot1 = this.getSlot('ordinal', 1)
    const slot2 = this.getSlot('ordinal', 2)

    const buttonsDisabled = requestState === 'PENDING'

    return (
      <ContentWrapper className='rcs-tote-edit'>
        <div className='rcs-tote-edit__actions'>
          <Button disabled={buttonsDisabled} btnType='secondary' onClick={this.onDelete}>
            {t('Delete')}
          </Button>
          <Button disabled={buttonsDisabled} onClick={this.onUpdate}>
            {t('Save')}
          </Button>
          <CloseIcon className='rcs-tote-edit__close-icon' onClick={this.onClose} />
        </div>

        {requestState === 'LOADING' ? (
          'Loading...'
        ) : (
          <>
            <div className='rcs-tote-edit__general-info'>
              <Label label={t('Tote ID')}>
                <Input
                  placeholder={t('Tote ID')}
                  value={tote.tote_id}
                  name='tote_id'
                  onChange={({ e: target }) => this.onFormChange(target.name, target.value)}
                />
              </Label>
              <Label label={t('Status')}>
                <Select
                  placeholder={t('Status')}
                  data={toteStatuses}
                  value={tote.tote_status}
                  onSelect={value => this.onFormChange('tote_status', value)}
                />
              </Label>
              <Label label={t('Orientation')}>
                <Select
                  placeholder={t('Orientation')}
                  data={toteOrientations}
                  value={tote.tote_orientation}
                  onSelect={value => this.onFormChange('tote_orientation', value)}
                />
              </Label>
              <Label label={t('Height')}>
                <Select
                  placeholder={t('Height')}
                  data={toteHeights}
                  value={tote.tote_height}
                  onSelect={value => this.onFormChange('tote_height', value)}
                />
              </Label>
              <Label label={t('Partitioning')}>
                <Select
                  placeholder={t('Partitioning')}
                  data={totePartitionings}
                  value={tote.tote_partitioning}
                  onSelect={value => this.onFormChange('tote_partitioning', value)}
                />
              </Label>
            </div>

            <div className='rcs-tote-edit__slots'>
              <Slot data={slot0} index={0} onChange={this.onSlotChange} />
              <Slot data={slot1} index={1} onChange={this.onSlotChange} />
              <Slot data={slot2} index={2} onChange={this.onSlotChange} />
            </div>
          </>
        )}
      </ContentWrapper>
    )
  }
}

export default compose(withTranslation(), withApi, withConfirmation)(ToteEdit)
