import React from 'react'
import PropTypes from 'prop-types'
import { useTranslation } from 'react-i18next'

import Button from 'components/Button'
import Checkbox from 'components/Checkbox'
import Label from 'components/Label'
import Input from 'components/Input'
import Select from 'components/Select'

import { orderStatuses } from 'config'

const Filters = props => {
  const { values, onChange, onApply, onPresetSave, onPresetSelect, presets } = props
  const { t } = useTranslation()

  function filtersHandler (id, value) {
    const nextFilters = { ...values, [id]: value }

    if (onChange) {
      onChange(nextFilters)
    }
  }

  function handleInputs (e) {
    const { name, value } = e.target
    filtersHandler(name, value)
  }

  return (
    <div>
      <div className='rcs-orders__filters-header'>
        <div className='rcs-orders__filters-title'>{t('Filters')}</div>
        <div className='rcs-orders__filters-presets'>
          <div className='rcs-orders__filters-presets-title'>{t('Select pre-saved preset')}</div>
          <Select
            placeholder={t('Filter presets')}
            data={presets}
            itemToString={x => x.filter_id}
            itemToValue={x => x}
            onSelect={onPresetSelect}
          />
        </div>
      </div>

      <div className='rcs-orders__filters-fields'>
        <Label label={t('Order status')}>
          <Select
            placeholder={t('Order status')}
            onSelect={x => filtersHandler('order_status', x)}
            data={orderStatuses}
            value={values.order_status}
          />
        </Label>
        <Label label={t('SKU')}>
          <Input placeholder={t('SKU')} onChange={handleInputs} name='sku' value={values.sku} />
        </Label>
        <Label label={t('Storage Tote')}>
          <Input
            placeholder={t('Storage Tote')}
            onChange={handleInputs}
            name='storage_tote'
            value={values.storage_tote}
          />
        </Label>
        <Label label={t('Delivery Tote')}>
          <Input
            placeholder={t('Delivery Tote')}
            onChange={handleInputs}
            name='delivery_tote'
            value={values.delivery_tote}
          />
        </Label>

        <div className='rcs-orders__filters-fields-buttons'>
          <Checkbox
            onChange={e => filtersHandler('only_in_progress', e.target.checked)}
            checked={values.only_in_progress}
          >
            {t('Only In Progress')}
          </Checkbox>
          <Button onClick={onApply}>{t('Apply')}</Button>
          <Button onClick={onPresetSave}>{t('Save Preset')}</Button>
        </div>
      </div>
    </div>
  )
}

Filters.propTypes = {
  values: PropTypes.object,
  onChange: PropTypes.func,
  onApply: PropTypes.func,
  onPresetSave: PropTypes.func,
  onPresetSelect: PropTypes.func,
  presets: PropTypes.array,
}

export default Filters
