import React from 'react'
import { compose } from 'recompose'
import { withTranslation } from 'react-i18next'

import Button from 'components/Button'
import Filter from 'components/Filter'
import ItemSelect from 'components/ItemSelect'
import Label from 'components/Label'
import Loader from 'components/Loader'
import Select from 'components/Select'
import Steps from 'components/Steps'
import MountTransition from 'components/MountTransition'

import { withApi, withConfirmation, withSupplyInfo, withUser } from 'providers'
import { getProductCategories, getDeliveryDisplayName, parseError } from 'helpers'

const supplySteps = [
  {
    id: 'supply_selection',
    title: 'Select delivery',
  },
  {
    id: 'products_selection',
    title: 'Select 3 items on the list',
  },
  {
    id: 'supply_start',
    title: 'Press start to proceed',
  },
]

class Delivery extends React.PureComponent {
  state = {
    requestState: 'NONE',
    deliveries: [],
    products: [],
    selectedDelivery: this.props.supplyInfo.getDelivery(),
    hasPreselectedDelivery: !!this.props.supplyInfo.getDelivery(),
    filter: 'all',
    selectedProducts: [],
    currentStep: ['supply_selection'],
  }

  componentDidMount () {
    const { selectedDelivery } = this.state
    this._isMounted = true
    if (selectedDelivery) {
      this.productsFetch()
    }
    this.deliveriesFetch()
  }

  componentWillUnmount () {
    this._isMounted = false
  }

  beginRequest = request => {
    if (!this._isMounted) return
    this.setState({ requestState: 'LOADING' }, request)
  }

  pendingRequest = request => {
    if (!this._isMounted) return
    this.setState({ requestState: 'PENDING' }, request)
  }

  finishRequest = request => {
    if (!this._isMounted) return
    this.setState({ requestState: 'READY' }, request)
  }

  failRequest = request => {
    if (!this._isMounted) return
    this.setState({ requestState: 'ERROR' }, request)
  }

  deliveriesFetch = () => {
    const { api, t, confirmation, history } = this.props

    this.beginRequest(() =>
      api
        .supplyList({ only_for_today: false })
        .then(res => {
          if (!this._isMounted) return
          const deliveries = res.body.supply
          this.setState({ deliveries: deliveries, requestState: 'READY' })
        })
        .catch(err => {
          if (!this._isMounted) return
          const error = parseError(err)

          this.failRequest(() =>
            confirmation.openConfirmation({
              children: error.message,
              header: error.header,
              onConfirm: () => this.beginRequest(this.deliveriesFetch),
              onCancel: () => history.push('/supply/progress-preview'),
              confirmLabel: t('Retry'),
              type: 'error',
            }),
          )
        }),
    )
  }

  productsFetch = () => {
    const { api, supplyInfo, t, confirmation, history } = this.props
    const { selectedDelivery } = this.state

    if (!selectedDelivery) {
      return
    }

    this.beginRequest(() =>
      api
        .supplyItemsList({ supply_id: selectedDelivery.supply_id })
        .then(res => {
          if (!this._isMounted) return
          const products = res.body.items
          supplyInfo.setDelivery(selectedDelivery)

          this.setState({
            products: products,
            requestState: 'READY',
            currentStep: ['products_selection'],
          })
        })
        .catch(err => {
          if (!this._isMounted) return
          const error = parseError(err)

          this.failRequest(() =>
            confirmation.openConfirmation({
              children: error.message,
              header: error.header,
              onConfirm: () => this.beginRequest(this.productsFetch),
              onCancel: () => history.push('/supply/progress-preview'),
              confirmLabel: t('Retry'),
              type: 'error',
            }),
          )
        }),
    )
  }

  onFilterChange = filter => {
    this.setState({ filter })
  }

  onProductSelect = selectedProducts => {
    const currentStep = selectedProducts.length > 0 ? ['supply_start'] : ['products_selection']
    this.setState({ selectedProducts, currentStep })
  }

  onStart = () => {
    const { history, supplyInfo, api, confirmation, t } = this.props
    const { selectedProducts } = this.state
    const sku_ids = selectedProducts.map(x => x.sku_id)

    this.pendingRequest(() =>
      api
        .supplyRequestTote({ sku_ids })
        .then(() => {
          if (!this._isMounted) return
          supplyInfo.setCurrentProducts(selectedProducts, () => history.push('/supply/loading'))
        })
        .catch(err => {
          if (!this._isMounted) return
          const error = parseError(err)

          this.finishRequest(() =>
            confirmation.openConfirmation({
              children: error.message,
              header: error.header,
              onConfirm: () => this.onStart(),
              confirmLabel: t('Retry'),
              type: 'error',
            }),
          )
        }),
    )
  }

  render () {
    const { t, user } = this.props
    const {
      currentStep,
      deliveries,
      filter,
      hasPreselectedDelivery,
      selectedDelivery,
      products,
      requestState,
      selectedProducts,
    } = this.state

    // Are deliveries in loading state?
    const isLoading = requestState === 'LOADING'

    if ((hasPreselectedDelivery && isLoading) || (!selectedDelivery && isLoading)) {
      return <Loader />
    }

    // Get products info
    const categories = getProductCategories(products)
    const filteredProducts =
      filter === 'all' ? products : products.filter(x => x.category_id === filter)

    // Check components state
    const hasProducts = Array.isArray(products) && products.length > 0
    const isConfirmBtnDisabled = !selectedDelivery || isLoading
    const isStartBtnDisabled = selectedProducts < 1 || isLoading || requestState === 'PENDING'

    // Manage deliver steps
    const steps = !!hasPreselectedDelivery
      ? supplySteps.filter(x => x.id !== 'supply_selection')
      : supplySteps

    return (
      <div className='rcs-delivery'>
        <Steps steps={steps} active={currentStep} />
        <div className='rcs-delivery__content'>
          {!hasProducts && (
            <h3>
              {t('Hi again {{user}}! Please select or confirm your delivery first', {
                user: user.displayName,
              })}
            </h3>
          )}

          <Label label={t('Delivery')}>
            <Select
              placeholder={t('Choose delivery')}
              data={deliveries}
              itemToString={x => getDeliveryDisplayName(x, t)}
              onSelect={x => this.setState({ selectedDelivery: x })}
              value={selectedDelivery}
              readOnly={!!hasPreselectedDelivery}
            />
          </Label>

          {hasProducts && (
            <MountTransition>
              <div className='rcs-delivery__filters'>
                <Filter filters={categories} onChange={this.onFilterChange} value={filter} />
              </div>
              <div className='rcs-delivery__products'>
                <ItemSelect
                  items={filteredProducts}
                  checkedItems={selectedProducts}
                  onChange={this.onProductSelect}
                />
              </div>
            </MountTransition>
          )}
        </div>

        {hasProducts ? (
          <Button
            className='rcs-delivery__button'
            btnType={isStartBtnDisabled ? 'neutral' : 'accent'}
            disabled={isStartBtnDisabled}
            onClick={this.onStart}
          >
            {t('Start')}
          </Button>
        ) : (
          <Button
            className='rcs-delivery__button'
            btnType={isConfirmBtnDisabled ? 'neutral' : 'accent'}
            disabled={isConfirmBtnDisabled}
            onClick={this.productsFetch}
          >
            {t('Confirm')}
          </Button>
        )}
      </div>
    )
  }
}

export default compose(
  withTranslation(),
  withApi,
  withUser,
  withSupplyInfo,
  withConfirmation,
)(Delivery)
